<template>
  <div class="flex items-center mt-5">
    <h2 class="intro-y text-lg font-medium mr-auto">
      <span class="uppercase">( {{ type }} )</span>
      Materi {{ detail.data.title }}
    </h2>
    <a
      class="button w-auto mr-2 mb-2 flex items-center justify-center bg-theme-1 text-white"
      :href="detail.data.content"
      target="_blank"
      v-if="type === 'pdf'"
    >
      <DownloadCloudIcon class="w-6 h-6 md:mr-2 mr-0" />
      <span class="hidden md:block">Download Document</span>
    </a>
  </div>
  <!-- BEGIN: Wizard Layout -->
  <div
    class="intro-y py-5 px-5 md:px-5 mt-5 w-auto max-h-screen embed-responsive aspect-ratio-4/3"
    v-if="type === 'pdf'"
  >
    <iframe
      class="m-auto max-w-full w-full"
      :src="'https://drive.google.com/viewerng/viewer?url='+ detail.data.content +'?pid=explorer&amp;efh=false&amp;a=v&amp;chrome=false&amp;embedded=true'"
      embedded="true"
    ></iframe>
  </div>
  <div
    class="intro-y py-5 px-5 md:px-5 mt-5 w-auto max-h-screen embed-responsive aspect-ratio-4/3"
    v-else
  >
    <!-- :src="'https://www.youtube.com/embed/' + getEmbed(detail.data.content)" -->
    <iframe
      class="m-auto max-w-full w-full"
      :src="detail.data.content"
      frameborder="0"
      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
      allowfullscreen=""
    ></iframe>
  </div>
  <!-- END: Wizard Layout -->
</template>

<script lang="ts">
import { defineComponent, reactive } from 'vue';
import { useRoute } from "vue-router";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const route = useRoute();
    const slug: any = route.params.slug;
    const routeName = route.name;
    const type = routeName === 'member-detail-tips-and-trik' ? 'video' : 'pdf';
    const detail = reactive({
      data: {},
      embed: "",
    })

    // console.log('route now ', route.name);
    // console.log('type and slug ', type , slug);

    const getData = async () => {
      const url = await api.getConfigFeatureMember('memberMateri', 'featureMateri') + `${type}/${slug}`;
      const hitApi = await api.hitApiGet(url);
      const rs = hitApi.data.response;
      detail.data = rs;
    }
    getData();

    const getEmbed = (url : string) => {
      return gpf.getParameterByNameWL(url, 'v');
    }

    return {
      slug,
      type,
      detail,
      getEmbed,
    }
  }
})
</script>
