<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div
          class="intro-y flex flex-col sm:flex-row items-center h-auto sm:h-10"
        >
          <h2 class="text-lg font-medium truncate">
            Leaderboard Hasil CAT Online
          </h2>
          <a
            href="javascript:;"
            class="ml-auto mt-2 sm:mt-0 flex text-theme-1 dark:text-theme-10"
            @click="getData()"
          >
            <RefreshCcwIcon class="w-4 h-4 mr-3" /> <span class="hidden md:block">Reload Data</span>
          </a>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
            <p
              class="text-base mr-auto md:mr-2 mb-3 md:mb-0 whitespace-nowrap text-left"
            >
              Filter :
            </p>
            <div class="w-full flex flex-row">
              <TailSelect
                v-model="dataCP.valueFilterPeringkat"
                v-on:change="changeTS($event, 'valueFilterPeringkat')"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in dataCP.listFilterPeringkat"
                  v-bind:key="idx"
                  :value="s.kode"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
              <button
                @click="getData()"
                class="button bg-theme-1 hover:bg-primary text-white w-24 ml-2"
              >
                View
              </button>
            </div>
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataCP.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                @click="getData()"
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0 cursor-pointer"
              />
            </div>
          </div>
        </div>
        <LoadingDiv id="loadingCP" />
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataCP.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.kode }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.nama }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.jenis }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $gpf.trimTextDash( u.totalSkor ) }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.status }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.tanggal, "DD MMM, YYYY") }}
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataCP.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Peringkat CAT.</p>
          </div>
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataCP.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataCP.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataCP.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataCP.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataCP.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataCP.page !== dataCP.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataCP.page !== dataCP.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataCP.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const dataCP = reactive({
      data: [] as any,
      valueFilterPeringkat: "skd",
      search: "",
      page: 1,
      pageSize: 10,
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      listFilterPeringkat: [],
    });

    const titleTable: Array<string> = [
      "Kode",
      "Nama Peserta",
      "Jenis",
      "Total Skor",
      "Status",
      "Tanggal",
    ];

    const getData = async () => {
      cash("#loadingCP").show();

      const hide = () => cash("#loadingCP").hide();
      const page = dataCP.page;
      const pageSize = dataCP.pageSize;
      const search = dataCP.search;

      const url = await api.getConfigFeatureMember('memberPeringkatCat', 'featurePeringkatCat') +
        dataCP.valueFilterPeringkat +
        "?search=" +
        search +
        "&page=" +
        page +
        "&pageSize=" +
        pageSize;
      const hitApi = await api.hitApiGet(url, hide);
      const ds = hitApi.data.diagnostic;
      const rs = hitApi.data.response;

      dataCP.data = rs;
      dataCP.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);

    };

    const getFilterCP = async () => {
      gpf.gLoading.show();

      const rs = await api.getJenisTryout();

      dataCP.listFilterPeringkat = rs;
      dataCP.valueFilterPeringkat = rs[0].kode ? rs[0].kode : "skd";

      await getData();
      gpf.gLoading.hide();
    };
    getFilterCP();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataCP.page = p;
      getData();
    };

    const nextPage = () => {
      dataCP.page = dataCP.page + 1;
      getData();
    };

    const beforePage = () => {
      dataCP.page = dataCP.page - 1;
      getData();
    };

    const lastPage = () => {
      dataCP.page = dataCP.paginate.totalPages;
      getData();
    };

    return {
      titleTable,
      dataCP,
      getData,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
    };
  },
  data() {
    return {};
  },
  methods: {
    changeTS(v: any, f: any) {
      const val = v.detail.args[1].key;
      const ex: any = this.dataCP;
      ex[f] = val;
    },
  },
});
</script>
