<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data Promo</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
						<button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addAP()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataAP.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                @click="getData()"
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0 cursor-pointer"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataAP.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.code }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.discount }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.type }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.isActive === 1 ? "Active" : " Suspend" }}
                </td>
								<td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editAP(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataAP.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Promo.</p>
          </div>
          <LoadingDiv id="loadingAP" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataAP.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataAP.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataAP.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataAP.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataAP.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataAP.page !== dataAP.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataAP.page !== dataAP.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataAP.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

		<!-- Modal Add and Edit Data Promo -->
    <div id="modal-data-ap" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Data Promo" : "Edit Data Promo" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-ap')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div 
            class="flex flex-col sm:flex-row items-center"
          >
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Kode Promo</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Kode Promo"
              v-model="exDataAP.code"
              :disabled="section === 'edit'"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Discount</label>
            <input
              type="email"
              class="input w-full border flex-1"
              placeholder="Discount"
              v-model="exDataAP.discount"
            />
          </div>
          <div 
						class="flex flex-col sm:flex-row items-center mt-3"
					>
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Type</label
            >
            <div class="w-1/2">
              <TailSelect
                v-model="exDataAP.type"
                v-on:change="changeTS($event, 'type')"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listType"
                  v-bind:key="idx"
                  :value="s.value"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
            </div>
          </div>
					<div 
						class="flex flex-col sm:flex-row items-center mt-3"
					>
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Status</label
            >
            <div class="w-1/2">
              <TailSelect
                v-model="exDataAP.status"
                v-on:change="changeTS($event, 'status')"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listStatus"
                  v-bind:key="idx"
                  :value="s.value"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-ap')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addAPApi()"
            v-if="section === 'add'"
          >
            Simpan
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editAPApi()"
            v-if="section === 'edit'"
          >
            Update
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
		const listStatus = gpf.dataStatus();

    const listType = [
      { 
        value: "percent",
        title: "Percent",
      },
      { 
        value: "price",
        title: "Price",
      },
    ];

    const titleTable: Array<string> = [
			"Kode",
      "Discount",
      "Type",
			"Status",
			"#",
    ];

    // "id": 9,
    // "code": "UPGRADE",
    // "discount": 90,
    // "type": "percent",
    // "isActive": true

    const dataAP = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingAP").show();

      const hide = () => cash("#loadingAP").hide();
      const page = dataAP.page;
      const pageSize = dataAP.pageSize;
      const search = dataAP.search;
      const url = api.affPromo +
        "?search=" +
        search +
        "&page=" +
        page +
        "&pageSize=" +
        pageSize;
      const hitApi = await api.hitApiGet(url, hide);
      const ds = hitApi.data.diagnostic;
      const rs = hitApi.data.response;

      dataAP.data = rs;
      dataAP.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
      
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataAP.page = p;
      getData();
    };

    const nextPage = () => {
      dataAP.page = dataAP.page + 1;
      getData();
    };

    const beforePage = () => {
      dataAP.page = dataAP.page - 1;
      getData();
    };

    const lastPage = () => {
      dataAP.page = dataAP.paginate.totalPages;
      getData();
    };

    return {
      titleTable,
      getData,
      dataAP,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
			listStatus,
      listType,
    };
  },
  data() {
    return {
			section: "add",

			exDataAP: {
				id: "",
				code: "",
				discount: "",
				type: "percent",
				status: "1",
			},

		};
  },
  methods: {
    changeTS(v: any, f: any) {
      const val = v.detail.args[1].key;
      const ex: any = this.exDataAP;
      ex[f] = val;
    },
		async emptyField() {
			const ex = this.exDataAP;
			ex.id = "0";
			ex.code = "";
			ex.discount = "";
			ex.type = "percent";
			ex.status = "1";
		},
		ctc(f: string) {
      const ex: any = this.exDataAP;
      ex[f] = gpf.ctc(ex[f]);
    },
		async addAP() {
      await this.emptyField();
      this.section = "add";
			gpf.showModal("#modal-data-ap");
    },
    async addAPApi() {
      gpf.gLoading.show();

      const hide = () => gpf.gLoading.hide();
      const ex = this.exDataAP;
      const params = new URLSearchParams();
      params.append("code", ex.code);
      params.append("discount", ex.discount);
      params.append("type", ex.type);
      params.append("status", ex.status);

      const url = api.affPromo;
      const hitApi = await api.hitApiPost(url, params, hide);
      await gpf.handleSds(hitApi);
      await gpf.hideModal("#modal-data-ap");
      await this.emptyField();
      await this.getData();
    },
    async editAP(data: any) {
      this.section = "edit";
      const ex = this.exDataAP;
      ex.id = data.id;
      ex.code = data.code;
      ex.discount = data.discount;
      ex.type = data.type;
      ex.status = data.isActive.toString();

      await gpf.showModal("#modal-data-ap");
    },
    async editAPApi() {
      gpf.gLoading.show();

      const hide = () => gpf.gLoading.hide();
      const ex = this.exDataAP;
      const params = new URLSearchParams();
      params.append("code", ex.code);
      params.append("discount", ex.discount);
      params.append("type", ex.type);
      params.append("status", ex.status);

      const url = api.affPromo + ex.code;
      const hitApi = await api.hitApiPut(url, params, hide);
      await gpf.handleSds(hitApi);
      await gpf.hideModal("#modal-data-ap");
      await this.emptyField();
      await this.getData();
    },

  },
});
</script>
