<template>
  <div class="grid grid-cols-12 gap-6">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex items-center h-10">
          <h2 class="text-lg font-medium truncate mr-5">CAT CPNS</h2>
          <a 
            href="javascript:;" 
            class="ml-auto flex text-theme-1 dark:text-theme-10"
            @click="getData();"
          >
            <RefreshCcwIcon class="w-4 h-4 mr-3" /> <span class="hidden md:block">Reload Data</span>
          </a>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="col-span-12 lg:col-span-12 xxl:col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y grid grid-cols-12 gap-3 sm:gap-6">
        <LoadingDiv id="loadingCatCpns" />
        <div
          v-for="(faker, fakerKey) in catCPNS.list"
          :key="fakerKey"
          class="intro-y col-span-6 sm:col-span-4 md:col-span-3 xxl:col-span-2"
        >
          <div
            class="file box rounded-md px-5 pt-5 pb-5 lg:pt-7 sm:px-5 relative zoom-in"
          >
            <a
              href="javascript:;"
              class="w-5/5 lg:w-5/6 file__icon file__icon--image mx-auto"
              @click="saveUrl(faker);"
            >
              <div class="file__icon--image__preview image-fit">
                <img
                  alt="Midone Tailwind HTML Admin Template"
                  :src="'/img/tryout-cpns.jpg'"
                />
              </div>
            </a>
            <a
              href="javascript:;"
              @click="saveUrl(faker);"
              class="block font-medium mt-4 text-center truncate"
            >{{ faker.title }}
            </a>
          </div>
        </div>
      </div>
      <!-- END: Directory & Files -->
    </div>
    <div id="modal-cat-cpns" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">PERHATIAN !!!</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="gpf.hideModal('#modal-cat-cpns');"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <p class="text-base font-medium mb-3">Sebelum mulai mengerjakan Tryout ini pastikan terlebih dahulu :</p>
          <ol class="pl-5" style="list-style: decimal">
            <li>Pastikan Anda menggunakan Perangkat Komputer atau Laptop dengan jaringan Internet yang stabil.</li>
            <li>Waktu Anda sudah siap karena untuk mengerjakan tryout dibutuhkan waktu {{ catCPNS.dataPG.durasi }} menit.</li>
            <li>Pastikan Anda sudah menguasai materi yang telah kami berikan.</li>
            <li>Siapkan alat bantu tulis disamping Anda untuk melakukan catatan-catatan kecil pada saat mulai mengerjakan soal.</li>
            <li>Yakinkan diri Anda untuk bisa berhasil menyelesaikan paket tryout ini dengan bisa menduduki peringkat teratas pada perengkingan.</li>
          </ol>
          <h5 class="text-base font-medium my-3">Nilai Ambang Batas : {{ catCPNS.dataPG.title }} </h5>
          <ol class="pl-5" style="list-style: decimal">
            <li>Passing Grade = {{ catCPNS.dataPG.nilaiPg }}</li>
            <li>Jumlah Soal = {{ catCPNS.dataPG.jmlSoal }}</li>
            <li>Durasi {{ catCPNS.dataPG.durasi }} menit.</li>
          </ol>
          <div v-if="catCPNS.dataPG.jurusan.length !== 0">
            <h5 class="text-base font-medium my-3">Pilih Jurusan : {{ catCPNS.dataPG.title }} </h5>
            <TailSelect
              v-model="catCPNS.dataPG.selectJurusan"
              v-on:change="changeTS($event, 'selectJurusan')"
              :options="{
                classNames: 'w-full',
              }"
            >
              <option
                v-for="(js, idx) in catCPNS.dataPG.jurusan"
                v-bind:key="idx"
                :value="js.kode"
              >
                {{ js.title }}
              </option>
            </TailSelect>
          </div>
          <p class="text-base font-medium text-center mt-3">Pastikan Anda Sudah Berdo'a</p>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="gotoTryoutExam()"
          >
            Mulai
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { useRouter } from "vue-router";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  components: {},
  setup() {
    const router = useRouter();
    const catCPNS = reactive({
      list: [],
      dataPG: {
        kode: "",
        title: "",
        nilaiPg: 0,
        jmlSoal: 0,
        durasi: 0,
        jurusan: [],
        selectJurusan: "",
      }
    });

    const saveUrl = async (data: any) => {
      gpf.gLoading.show();
      const hide = () => gpf.gLoading.hide();

      const url = await api.getConfigFeatureMember('memberPassingGrade', 'featurePassingGrade') + data.kode;
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;
      // console.log('response get pg ', rs);
      catCPNS.dataPG = rs;
      if ( gpf.trimText(rs.jurusan) ) {
        catCPNS.dataPG.jurusan = rs.jurusan;
        catCPNS.dataPG.selectJurusan = rs.jurusan[0].kode;
      } else {
        catCPNS.dataPG.jurusan = [];
        catCPNS.dataPG.selectJurusan = "";
      }
      gpf.showModal("#modal-cat-cpns");

    }

    const gotoTryoutExam = () => {
      gpf.hideModal("#modal-cat-cpns");
      gpf.gLoading.show();
      setTimeout(() => {
        if ( catCPNS.dataPG.jurusan.length !== 0 ) {
            router.push('cat-cpns/'+ catCPNS.dataPG.kode + '?jurusan=' + catCPNS.dataPG.selectJurusan);
        } else {
          router.push('cat-cpns/'+ catCPNS.dataPG.kode);
        }
      }, 700);
    }

    const getData = async () => {
      cash("#loadingCatCpns").show();
      const hide = () => cash("#loadingCatCpns").hide();

      const url = await api.getConfigFeatureMember('memberJenisTryout', 'featureJenisTryout') + 'cpns';
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;
      catCPNS.list = rs;

    };
    getData();

    return {
      saveUrl,
      gotoTryoutExam,
      catCPNS,
      getData,
    };
  },
  methods: {
    changeTS(v: any, f: any) {
      const val = v.detail.args[1].key;
      const ex: any = this.catCPNS.dataPG;
      ex[f] = val;

      console.log("this.catCPNS ", this.catCPNS);
    },
  }
});
</script>
