<template>
  <div>
    <div
      class="flex justify-center md:justify-between flex-col sm:flex-row items-center mt-8"
    >
      <h2 class="intro-y text-lg font-medium m-auto md:mr-auto md:ml-0">
        {{ dataExam.title }}
      </h2>
      <div
        class="md:grid grid-cols-12 hidden w-36 items-center button p-0 justify-center text-white cursor-pointer"
        @click="$gpf.showModal('#done-tryout-exam')"
      >
        <span
          class="inline-block col-span-4 py-3 px-1 bg-red-700 text-center rounded rounded-r-none"
        >
          X
        </span>
        <span
          class="inline-block col-span-8 p-3 bg-red-600 text-center rounded rounded-l-none"
          >Selesai</span
        >
      </div>
    </div>
    <!-- BEGIN: Wizard Layout -->
    <div class="intro-y grid grid-cols-12 gap-3 sm:gap-2 mt-5">

      <div class="intro-y col-span-12 box px-3 sm:px-5 pb-5">
        <div
          class="flex flex-row items-center justify-between py-5 border-b border-gray-200 dark:border-dark-5"
        >
          <div
            class="grid grid-cols-12 w-24 sm:w-32 button p-0 items-center justify-center text-white cursor-pointer"
          >
            <span
              class="inline-block col-span-5 sm:col-span-4 py-3 px-1 bg-blue-800 text-center rounded rounded-r-none"
              >NO</span
            >
            <span
              class="inline-block col-span-7 sm:col-span-8 p-3 bg-blue-600 text-center rounded rounded-l-none"
              >{{ cs.nomor }}</span
            >
          </div>
          <div
            class="grid grid-cols-12 w-36 sm:w-40 button p-0 items-center justify-center text-white"
          >
            <span
              class="inline-block col-span-5 py-3 px-1 bg-yellow-400 text-center rounded rounded-r-none"
              >Waktu</span
            >
            <span
              class="inline-block col-span-7 p-3 bg-yellow-300 text-center rounded rounded-l-none"
              >{{ time.hours }}:{{ time.minutes }}:{{ time.seconds }}</span
            >
          </div>
        </div>

        <!-- BEGIN Question Soal -->
        <div class="pt-5 pb-2">
          <h3 class="text-base font-medium mt-1 mb-1 uppercase">
            {{ cs.kategori }}
          </h3>
          <img v-if="cs.questionImg" :src="cs.questionImg" class="m-2" alt="Image Soal">
          <p
            class="border-b border-gray-500 dark:border-gray-600 p-2 mb-2 bg-gray-200 dark:bg-dark-1 text-base"
            v-html="cs.question"
          ></p>
          <ol>
            <li
              v-for="(opt, idx) in cs.option"
              v-bind:key="idx"
              :opt="'chs-' + cs.nomor + '-' + idx"
              :class="[
                'flex flex-row justify-items-center my-3 md:my2 cursor-pointer button p-0 list-chs',
                cs.answer && cs.answerIdx === idx ? 'active' : '',
              ]"
              @click="chooseOpt(cs, cs.nomor, idx)"
            >
              <span
                class="inline-block px-2 py-2 w-8 rounded rounded-r-none text-center border border-gray-500 dark:border-gray-600"
                >{{ opt.option }}
              </span>
              <span
                class="inline-block pl-2 sm:pl-3 pr-2 py-2 w-full rounded rounded-l-none text-left border border-gray-500 dark:border-gray-600"
              >
                <img v-if="opt.image" :src="opt.image" alt="Image Opsi Soal">
                <br v-if="opt.image" />
                {{ opt.text }}
              </span>
            </li>
          </ol>
        </div>
        <!-- END Question Soal -->

        <!-- BEGIN Pointer Soal -->
        <div
          class="flex flex-row items-center justify-center justify-items-center pt-5 border-t border-gray-200 dark:border-dark-5"
        >
          <button
            v-if="cis !== 0"
            class="button w-auto h-11 mr-2 md:mr-5 pl-1 md:pr-5 flex items-center justify-center bg-theme-1 text-white"
            @click="changeSoalCis('back')"
          >
            <ChevronLeftIcon class="w-6 h-6 ml-1 mr-0 sm:mr-2 font-bold" />
            <span class="hidden sm:block">Back</span>
          </button>
          <button
            class="button w-32 h-11 flex items-center justify-center bg-theme-12 text-white"
            @click="
              allSoal[cis].ragu = !allSoal[cis].ragu;
              setRagu();
            "
          >
            <HelpCircleIcon class="w-6 h-6 mr-2 font-bold" /> Ragu-ragu
          </button>
          <button
            v-if="cis !== allSoal.length - 1"
            class="button w-auto h-11 ml-2 md:ml-5 pl-3 md:pl-5 md:pr-1 pr-2 sm:pr-1 flex items-center justify-center bg-theme-1 text-white flex-row-reverse"
            @click="changeSoalCis('next')"
          >
            <ChevronRightIcon class="w-6 h-6 ml-0 sm:ml-2 font-bold mr-auto" />
            <span class="hidden sm:block">Next</span>
          </button>
          <button
            v-else
            class="button w-auto h-11 ml-2 md:ml-5 pl-2 md:pl-4 md:pr-2 pr-2 flex items-center justify-center bg-green-500 dark:bg-green-700 text-white flex-row-reverse"
            @click="$gpf.showModal('#done-tryout-exam')"
          >
            <CheckIcon class="w-6 h-6 ml-0 sm:ml-2 font-bold mr-auto" />
            <span class="hidden sm:block">Done</span>
          </button>
        </div>
        <!-- END Pointer Soal -->

      </div>

      <!-- BEGIN Navigasi Soal -->
      <div class="intro-y col-span-12 box p-2">
        <div
          class="intro-y col-span-12 flex flex-row justify-between border-b border-gray-200 dark:border-dark-5 pt-2 pb-3"
        >
          <h3 class="intro-y text-lg font-medium w-full">Navigasi Soal</h3>
          <a
            id="cvRn"
            @click="rn()"
            href="javascript:;"
            class="font-bold cursor-pointer"
          >
            <ChevronDownIcon class="w-6 h-6 mr-0" />
          </a>
        </div>
        <div class="w-auto overflow-auto scrollbar-nav-soal mb-3">
          <ol id="nsRn" class="flex flex-row">
            <li
              v-for="(soal, idx) in allSoal"
              v-bind:key="idx"
              :class="[
                'my-2 px-2 list-ns',
                idx === cis ? 'active' : '',
                soal.ragu ? 'ragu' : '',
                soal.answer && !soal.ragu ? 'yakin' : '',
              ]"
            >
              <button
                class="intro-y w-16 h-10 rounded button bg-gray-200 dark:bg-dark-1 text-gray-600 px-1"
                @click="changeSoal(soal, idx)"
              >
                {{ soal.nomor }} - {{ soal.answer ? soal.answer : "" }}
              </button>
            </li>
          </ol>
        </div>
      </div>
      <!-- BEGIN Navigasi Soal -->

    </div>
    <!-- END: Wizard Layout -->

    <div
      class="flex justify-between flex-col sm:flex-row items-center mt-8 md:mt-0"
    >
      <div
        class="grid grid-cols-12 md:hidden w-36 items-center button p-0 justify-center text-white cursor-pointer"
        @click="$gpf.showModal('#done-tryout-exam')"
      >
        <span
          class="inline-block col-span-4 py-3 px-1 bg-red-700 text-center rounded rounded-r-none"
        >
          X
        </span>
        <span
          class="inline-block col-span-8 p-3 bg-red-600 text-center rounded rounded-l-none"
          >Selesai</span
        >
      </div>
    </div>

    <!-- Modal Selesai Tryout -->
    <div id="done-tryout-exam" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <HelpCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Are you sure?</div>
          <div class="text-gray-600 mt-2">
            Apakah Anda ingin mengakhiri simulasi ujian ini?
            Jika "Ya" maka Anda sudah dinyatakan selesai mengikuti simulasi ujian, dan Anda tidak bisa memperbaiki lembar kerja Anda. Jika "Tidak" maka Anda akan kembali ke lembar kerja dan silahkan untuk melanjutkan menjawab atau memperbaiki jawaban Anda. 
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
          >
            Tidak
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            data-dismiss="modal"
            @click="tryoutHasil()"
          >
            Ya
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { useRoute, useRouter } from "vue-router";
// import { dataExam } from "./tryout-exam";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const route = useRoute();
    const router = useRouter();

    const params = route.params;
    const type = params.slug;
    const loc = window.location;

    // const resp = dataExam.response;
    // const soalList = dataExam.response.soal;
    // console.log('soalList ', soalList.length, type);

    gpf.hideModal("#modal-cat-cpns");
    gpf.hideModal("#modal-cat-pppk");

    const rn = (): void => {
      cash("#cvRn").toggleClass("transform rotate-180");
      cash("#nsRn").toggleClass("flex-wrap justify-center");
    };

    return {
      params,
      rn,
      router,
      type,
      loc,
      // resp,
      // soalList,
    };
  },
  data() {
    return {
      cs: {
        id: 0,
        nomor: 0,
        kategori: "",
        bidang: "",
        question: "",
        questionImg: "",
        option: [
          {
            option: "",
            text: "",
            img: "",
          },
        ],
      },
      allSoal: [] as any[],
      cis: 0,
      time: {
        hours: "00",
        minutes: "00",
        seconds: "00",
      },
      timeExpired: false,
      urlExam: "",
      dataExam: {
        kode: "",
        type: "",
        startDate: "",
        endDate: "",
        duration: 0,
        title: "",
        soal: [],
      },
      countDownDate: 0,
    };
  },
  methods: {
    async getSoalCat(jrs: string) {
      
      const url = this.urlExam;
      const hitApi = await api.hitApiGet(url);
      const rs = hitApi.data.response;
      this.dataExam = rs;
      this.allSoal = rs.soal;
      this.countDownDate = rs.endDate;
      this.cs = this.allSoal[this.cis];
      this.countDownDate = new Date(rs.endDate).getTime();

      gpf.setUpPayloadSubmitHasilCat(rs.soal, rs.kode);
      gpf.setUpDataCat(rs, rs.kode);
      this.countTime();

      if ( jrs === 'with' ) {
        await this.withJurusan();
      } else {
        await this.withoutJurusan();
      }

    },
    async tryoutHasil() {
      // console.log("loc.search ", this.loc.search);
      gpf.gLoading.show();

      const doneExam = () => console.log("Tryout Exam Done");
      const kodeExam = gpf.getParameterByName("kode");
      const jwbCat: any = JSON.parse(gpf.memberHasilCat(kodeExam));

      const url = await api.getConfigFeatureMember('memberSubmitCat', 'featureSubmitCat') + kodeExam;
      await api.hitApiPost(url, jwbCat, doneExam, api.configGet());
      await gpf.removeStorageCat(kodeExam);
      this.router.push(this.type + "/hasil" + this.loc.search);

    },
    changeSoal(soal: any, idx: number): void {
      this.cs = soal;
      this.cis = idx;
    },
    changeSoalCis(tp: string): void {
      tp === "next" ? this.cis++ : this.cis--;
      this.cs = this.allSoal[this.cis];
    },
    chooseOpt(cs: any, no: any, idx: any): void {
      const a = cs.option;
      for (let i = 0; i < a.length; i++) {
        if (i !== idx) {
          cash('[opt="chs-' + no + "-" + i + '"]').removeClass("active");
        } else {
          cash('[opt="chs-' + no + "-" + idx + '"]').addClass("active");
          this.allSoal[this.cis].answer = cs.option[idx].option;
          this.allSoal[this.cis].answerIdx = idx;
          gpf.setUpPayloadSubmitHasilCat(this.allSoal, this.dataExam.kode);
        }
      }
    },
    setRagu() {
      gpf.setUpPayloadSubmitHasilCat(this.allSoal, this.dataExam.kode);
    },
    async countTime() {
      // console.log("masuk ke countTime ", this.countDownDate);
      // Update the count down every 1 second
      const x = setInterval(async () => {
        // Get today's date and time
        const now = new Date().getTime();

        // Find the distance between now and the count down date
        const distance = this.countDownDate - now;

        // Time calculations for days, hours, minutes and seconds
        // const days = Math.floor(distance / (1000 * 60 * 60 * 24));
        const hours = Math.floor(
          (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        );
        const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((distance % (1000 * 60)) / 1000);

        this.time.hours = hours.toString();
        this.time.minutes = minutes.toString();
        this.time.seconds = seconds.toString();

        // Display the result in the element with id="demo"

        // If the count down is finished, write some text
        if (distance < 0) {
          clearInterval(x);
          this.timeExpired = true;

          await this.tryoutHasil();
        }
      }, 1000);
    },
    async withoutJurusan() {
      let newurl = "";
      newurl =
        this.loc.protocol +
        "//" +
        this.loc.host +
        this.loc.pathname +
        "?kode=" +
        this.dataExam.kode;
      window.history.pushState({ path: newurl }, "Page Exam", newurl);

      gpf.gLoading.hide();
    },
    async withJurusan() {
      const jurusan = gpf.getParameterByName("jurusan");
      let newurl = "";

      newurl =
        this.loc.protocol +
        "//" +
        this.loc.host +
        this.loc.pathname +
        "?jurusan=" +
        jurusan +
        "&kode=" +
        this.dataExam.kode;
      window.history.pushState({ path: newurl }, "Page Exam", newurl);

      gpf.gLoading.hide();
    }
  },
  async mounted() {
    const jurusan = gpf.getParameterByName("jurusan");
    const kodeExam = gpf.getParameterByName("kode");

    if (kodeExam === null && jurusan === null) {
      this.urlExam = await api.getConfigFeatureMember('memberSoalCat', 'featureSoalCat') + this.type;
      await this.getSoalCat('without');

    } else if (jurusan !== null) {
      this.urlExam = await api.getConfigFeatureMember('memberSoalCat', 'featureSoalCat') + this.type + "?jurusan=" + jurusan;
      await this.getSoalCat('with');

    } else {
      const dataCat: any = JSON.parse(gpf.getDataCat(kodeExam));
      // console.log("dataCat ", dataCat.endDate);
      this.countDownDate = new Date(dataCat.endDate).getTime();
      this.countTime();
      this.dataExam = dataCat;
      this.allSoal = JSON.parse(gpf.getSoalCat(kodeExam));
      this.cs = this.allSoal[this.cis];
    }
  },
});
</script>

<style lang="scss" scoped>
.list-chs {
  &.active {
    > span {
      &:first-child {
        @apply bg-green-700 dark:bg-green-800 text-white;
      }
      &:last-child {
        @apply bg-green-500 dark:bg-green-600 text-white;
      }
    }
  }
}
.list-ns {
  &.active {
    > button {
      @apply bg-primary text-white;
    }
  }
  &.ragu {
    > button {
      @apply bg-yellow-300 text-white;
    }
  }
  &.yakin {
    > button {
      @apply bg-green-500 dark:bg-green-700 text-white;
    }
  }
}
</style>
