<template>
  <div class="intro-y flex sm:flex-row items-center mt-3 h-auto">
    <h2 class="text-lg font-medium truncate mx-auto md:mx-0">
      Upgrade Ke Platinum
    </h2>
  </div>

  <!-- BEGIN: Pricing Layout -->
  <div class="grid grid-cols-2 gap-7 md:gap-5 mt-8 md:mt-5 px-5">
    <div class="intro-y flex-1 col-span-2 md:col-span-1 rounded-md">
      <div class="flex flex-col zoom-in cursor-pointer box py-16 px-5">
        <div
          class="rounded-full w-20 h-20 mx-auto flex items-center justify-center shadow-xl dark:bg-gray-900"
        >
          <StarIcon class="w-9 h-9 text-theme-1 dark:text-theme-10 mx-auto" />
        </div>
        <div class="text-2xl font-medium text-center mt-10">Paket Premium</div>
        <ol class="text-gray-700 dark:text-gray-600 text-center mt-5">
          <li class="border-b border-gray-400 py-3">
            Unlimited Tryout CPNS & PPPK
          </li>
          <li class="border-b border-gray-400 py-3">CAT SKD</li>
          <li class="border-b border-gray-400 py-3">CAT SKB</li>
          <li class="border-b border-gray-400 py-3">CAT PPPK</li>
          <li class="border-b border-gray-400 py-3">Materi Penunjang</li>
          <li class="border-b border-gray-400 py-3">Group & Chanel</li>
          <li class="border-b border-gray-400 py-3 line-through">
            Video Tips & Trik
          </li>
          <li class="border-b border-gray-400 py-3 line-through">
            Kelas Privat & Streaming
          </li>
        </ol>
        <div class="flex justify-center">
          <div class="relative text-5xl font-semibold mt-8 mx-auto">
            <span
              class="absolute text-2xl top-0 left-0 text-gray-500 -ml-10"
              >IDR</span
            >
            {{ $gpf.ctc(dataUA.pricePremium.price) }}
          </div>
        </div>
        <button
          type="button"
          class="button button--lg block text-white bg-theme-1 rounded-full mx-auto mt-8"
          @click="pilihPaket(0)"
          :disabled="paketNow === 'premium' || paketNow === 'platinum'"
        >
          Upgrade Paket
        </button>
      </div>
    </div>
    <div class="intro-y flex-1 col-span-2 md:col-span-1 rounded-md">
      <div class="flex flex-col zoom-in cursor-pointer box py-16 px-5">
        <div
          class="rounded-full w-20 h-20 mx-auto flex items-center justify-center shadow-xl bg-theme-1 dark:bg-theme-10"
        >
          <StarIcon class="w-9 h-9 text-theme-2 mx-auto" fill="#1C3FAA" />
        </div>
        <div class="text-2xl font-medium text-center mt-10">Paket Platinum</div>
        <ol class="text-gray-700 dark:text-gray-600 text-center mt-5">
          <li class="border-b border-gray-400 py-3">
            Unlimited Tryout CPNS & PPPK
          </li>
          <li class="border-b border-gray-400 py-3">CAT SKD</li>
          <li class="border-b border-gray-400 py-3">CAT SKB</li>
          <li class="border-b border-gray-400 py-3">CAT PPPK</li>
          <li class="border-b border-gray-400 py-3">Materi Penunjang</li>
          <li class="border-b border-gray-400 py-3">Group & Chanel</li>
          <li class="border-b border-gray-400 py-3">Video Tips & Trik</li>
          <li class="border-b border-gray-400 py-3">
            Kelas Privat & Streaming
          </li>
        </ol>
        <div class="flex justify-center">
          <div class="relative text-5xl font-semibold mt-8 mx-auto">
            <span
              class="absolute text-2xl top-0 left-0 text-gray-500 -ml-10"
              >IDR</span
            >
            {{ $gpf.ctc(dataUA.pricePlatinum.price) }}
          </div>
        </div>
        <button
          type="button"
          class="button button--lg block text-white bg-theme-1 rounded-full mx-auto mt-8"
          @click="pilihPaket(1)"
          :disabled="paketNow === 'platinum'"
        >
          Order Sekarang
        </button>
      </div>
    </div>
  </div>
  <!-- END: Pricing Layout -->

  <!-- Modal Pilih Paket -->
  <div id="modal-pilih-paket" class="modal p-0">
    <div class="modal__content modal__content--lg">
      <div
        class="flex items-center px-5 py-5 sm:py-3"
      >
        <h2 class="font-medium text-base mr-auto">Pilih Metode Pembayaran</h2>
        <a
          href="javascript:;"
          class="right-0 top-0"
          @click="$gpf.hideModal('#modal-pilih-paket')"
        >
          <xIcon class="w-6 h-6 text-gray-500" />
        </a>
      </div>
      <div class="px-3 md:px-5 pt-3 pb-5">
        <div class="flex flex-col justify-center items-center">
          <h3 class="text-3xl">{{ dataPaket.title }}</h3>
          <div class="relative text-2xl font-semibold mt-2 mx-auto">
            <span :class="['text-2xl text-gray-500', exDataUA.discount ? 'line-through': '']">IDR</span>
            <span :class="['mx-2', exDataUA.discount ? 'line-through': '']">{{ $gpf.ctc(dataPaket.price) }}</span>
          </div>
          <div
            v-if="exDataUA.discount"
            class="py-3 px-5 border-2 border-theme-9 rounded-lg mt-2 flex flex-col justify-center items-center"
          >
            <div class="relative text-3xl font-semibold mx-auto">
              <span class="text-3xl text-gray-500">IDR</span>
              <span :class="['mx-2']">{{ $gpf.ctc(exDataUA.priceDiscount) }}</span>
            </div>
            <h5 class="text-theme-9 text-base">"Selamat, Anda mendapatkan Promo."</h5>
          </div>
        </div>
        <div 
          class="flex flex-col items-center mt-5"
        >
          <label class="w-full mb-1 font-semibold">Masukan Kode Promo</label>
          <div class="flex flex-row w-full whitespace-nowrap">
            <input
              type="text"
              class="input w-full border"
              placeholder="Kode Promo"
              v-model="exDataUA.kode"
              v-on:keyup.enter="validasiPromo()"
            />
            <button
              type="button"
              class="button block text-white bg-theme-9 rounded mx-auto ml-3"
              @click="validasiPromo()"
            >
              Check
            </button>
          </div>
        </div>
        <div class="flex flex-col mt-3">
          <label class="w-full sm:w-28 sm:mr-5 font-semibold mb-1">Payment :</label>
          <div class="w-full">
            <TailSelect
              v-model="exDataUA.paymentId"
              v-on:change="changeTS($event, 'paymentId')"
              :options="{
                classNames: 'w-full',
              }"
            >
              <optgroup 
                v-for="(pp, idx) in listPayment"
                v-bind:key="idx"
                :label="pp.label"
              >
                <option
                  v-for="(ss, idx) in pp.options"
                  v-bind:key="idx"
                  :value="ss.value"
                  @click="exDataUA.paymentId = ss.value"
                >
                  {{ ss.text }}
                </option>
              </optgroup>
            </TailSelect>
          </div>
        </div>
      </div>
      <div
          class="px-5 py-3 text-center"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-pilih-paket')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="buyPaket()"
          >
            Buy
          </button>
        </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { api } from "@/utils/api-config";
import { gpf } from "@/utils/global-functions";
import Toastify from "toastify-js";

export default defineComponent({
  setup() {
    const ld:any = api.getLoginData();
    const paketNow = ld.paket;
    const titleTable: Array<string> = [
      "Item",
      "Price",
      "Discount",
      "SubTotals",
    ];

    const dataUA = reactive({
      data: [
        {
          id: 1,
          title: "",
          price: 0,
        },
        {
          id: 2,
          title: "",
          price: 0,
        },
      ] as any,
      pricePremium: {
        price: "0",
        newPrice: "0",
      },
      pricePlatinum: {
        price: "0",
        newPrice: "0",
      },
    });

    const getPackage = async () => {
      gpf.gLoading.show();

      const hitApi = await api.getPackage();
      dataUA.data = hitApi;
      dataUA.pricePremium.price = hitApi[0].price;
      dataUA.pricePlatinum.price = hitApi[1].price;

      gpf.gLoading.hide();
    };
    getPackage();

    return {
      titleTable,
      dataUA,
      getPackage,
      paketNow,
    };
  },
  data() {
    return {
      exDataUA: {
        packageId: "",
        paymentId: "",
        promoId: "",
        kode: "",
        discount: false,
        priceDiscount: "0",
      },
      listPayment: [],
      dataPaket: {
        id: 1,
        title: "",
        price: 0,
      } as any,
    };
  },
  methods: {
    changeTS(v: any, f: any) {
      const val = v.detail.args[1].key;
      const ex: any = this.exDataUA;
      ex[f] = val;
    },
    async pilihPaket(idx: number) {
      gpf.gLoading.show();
      const ex = this.exDataUA;
      const hitApi = await api.getPayment();
      this.listPayment = hitApi;
      ex.paymentId = hitApi[0].options[0].value;
      ex.packageId = this.dataUA.data[idx].id;

      this.dataPaket = this.dataUA.data[idx];
      this.dataPaket.idx = idx;

      gpf.showModal('#modal-pilih-paket');
      gpf.gLoading.hide();
    },
    async validasiPromo() {
      gpf.gLoading.show();
      
      const ex = this.exDataUA;
      const idx = this.dataPaket.idx;

      const hide = () => gpf.gLoading.hide();
      const url = api.publicPromo;
      const params = new URLSearchParams();
      params.append("kode", ex.kode);
      const hitApi = await api.hitApiPost(url, params, hide);
      console.log("hitApi ", hitApi);

      if ( hitApi.status === 200 ) {
        const rs = hitApi.data.response;
        ex.promoId = rs[idx].promoId;
        ex.discount = true;
        ex.priceDiscount = rs[idx].promoPrice;

        Toastify({
          text: "Selamat, Anda mendapatkan Promo.",
          duration: 5000,
          newWindow: true,
          close: true,
          gravity: "top",
          position: "right",
          backgroundColor: "#0e2c88",
          stopOnFocus: true,
        }).showToast();
      }

    },
    async buyPaket() {
      const ex = this.exDataUA;
      const slug = `${ex.packageId}-${ex.paymentId}-${ex.promoId === "" ? "0" : ex.promoId}`;

      await gpf.hideModal('#modal-pilih-paket');
      this.$router.push(`upgrade-akun/invoice/` + slug);
    }
  }
});
</script>
