<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Blog Artikel</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addBA()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataBA.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                @click="getData()"
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0 cursor-pointer"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataBA.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <div class="w-36">
                    <img
                      class="w-full"
                      :src="u.banner"
                      alt="Banner Image"
                      :title="'Banner ' + u.title"
                      data-action="zoom"
                    />
                  </div>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.title }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <span v-if="u.BlogCategories.length === 0">-</span>
                  <ul v-else >
                    <li
                      v-for="(bb, idx) in u.BlogCategories"
                      :key="idx"
                    >• {{ bb.title }}</li>
                  </ul>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <span v-if="u.BlogTags.length === 0">-</span>
                  <ul v-else >
                    <li
                      v-for="(tt, idx) in u.BlogTags"
                      :key="idx"
                    >• {{ tt.title }}</li>
                  </ul>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.isActive ? "Active" : "Suspend" }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.createdAt, 'DD MMM, YYYY HH:mm:ss') }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editBA(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                      @click="deleteBA(u)"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer text-theme-6"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataBA.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Blog Artikel.</p>
          </div>
          <LoadingDiv id="loadingBAadmin" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataBA.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataBA.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataBA.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataBA.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataBA.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataBA.page !== dataBA.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataBA.page !== dataBA.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataBA.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

    <!-- Modal Add and Edit Blog Artikel -->
    <div id="modal-data-ba" class="modal p-0">
      <div class="modal__content modal__content--xl">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Blog Artikel" : "Edit Blog Artikel" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-ba')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Title"
              v-model="exDataBA.title"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Keyword</label>
            <div class="fles flex-col w-full md:w-10/12 mx-auto">
              <textarea
                type="text"
                class="input w-full border flex-1"
                placeholder="Description"
                v-model="exDataBA.keyword"
                maxlength="200"
              ></textarea>
              <div class="text-xs text-gray-600 text-right">
                {{ exDataBA.keyword.length }} / 200
              </div>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Description</label>
            <div class="fles flex-col w-full md:w-10/12 mx-auto">
              <textarea
                type="text"
                class="input w-full border flex-1"
                placeholder="Description"
                v-model="exDataBA.description"
                maxlength="200"
              ></textarea>
              <div class="text-xs text-gray-600 text-right">
                {{ exDataBA.description.length }} / 200
              </div>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Content</label>
            <div class="preview w-full md:w-10/12 mx-auto">
              <CKEditor
                v-model="exDataBA.content"
                :editor="classicEditor"
                :config="editorConfig"
              />
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Kategori</label
            >
            <div class="w-1/2">
              <TailSelect
                v-model="exDataBA.kategori"
                v-on:change="changeTS($event)"
                :options="{
                  search: true,
                  descriptions: true,
                  hideSelected: true,
                  hideDisabled: true,
                  multiLimit: 15,
                  multiShowCount: false,
                  multiContainer: true,
                  classNames: 'w-full',
                }"
                multiple
              >
                <option
                  v-for="(s, idx) in listKategori"
                  v-bind:key="idx"
                  :value="s.id"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Tag</label
            >
            <div class="w-1/2">
              <TailSelect
                v-model="exDataBA.tag"
                v-on:change="changeTS($event)"
                :options="{
                  search: true,
                  descriptions: true,
                  hideSelected: true,
                  hideDisabled: true,
                  multiLimit: 15,
                  multiShowCount: false,
                  multiContainer: true,
                  classNames: 'w-full',
                }"
                multiple
              >
                <option
                  v-for="(s, idx) in listTag"
                  v-bind:key="idx"
                  :value="s.id"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Status</label
            >
            <div class="w-1/2">
              <TailSelect
                v-model="exDataBA.status"
                v-on:change="changeTS($event, 'status')"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listStatus"
                  v-bind:key="idx"
                  :value="s.value"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Banner</label>
            <div class="flex flex-col w-10/12">
              <div class="flex items-center mb-2">
                <input
                  type="file"
                  class="button w-10 border flex-1 hidden"
                  placeholder="Banner"
                  id="fileBannerDM"
                  v-on:change="handleFile($event)"
                />
                <button
                  v-if="exDataBA.banner === ''"
                  @click="chooseBanner()"
                  class="button bg-theme-1 hover:bg-primary text-white w-28 whitespace-nowrap"
                >
                  Choose File
                </button>
                <button
                  v-else
                  @click="removeBanner()"
                  class="button bg-theme-1 hover:bg-primary text-white w-32 whitespace-nowrap"
                >
                  Remove Banner
                </button>
                <span class="text-sm ml-2 truncate">{{ exDataBA.nameBanner }}</span>
              </div>
              <span class="text-sm">Maks Ukuran File Banner = 1MB.</span>
              <span
                v-if="exDataBA.oversizeBanner"
                class="text-sm text-red-700"
              >Ukuran Banner melebihi 1MB, Silahkan Pilih Banner kembali.</span>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Preview Image</label>
            <div class="show-image w-80">
              <img
                v-if="section === 'add'"
                id="showImage"
                src
                alt
              />
              <img
                v-if="section === 'edit'"
                id="showImage"
                :src="exDataBA.banner"
                alt
              />
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-ba')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addBAApi()"
            v-if="section === 'add'"
          >
            Simpan
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editBAApi()"
            v-if="section === 'edit'"
          >
            Update
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Delete Blog Artikel -->
    <div id="delete-modal-data-ba" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Title : "{{ exDeleteBA.title }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-ba')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="deleteBAApi();"
          >
            Delete
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

import ClassicEditor from "@ckeditor/ckeditor5-editor-classic/src/classiceditor";
import InlineEditor from "@ckeditor/ckeditor5-editor-inline/src/inlineeditor";
import BalloonEditor from "@ckeditor/ckeditor5-editor-balloon/src/ballooneditor";
import DocumentEditor from "@ckeditor/ckeditor5-editor-decoupled/src/decouplededitor";
import EssentialsPlugin from "@ckeditor/ckeditor5-essentials/src/essentials";
import BoldPlugin from "@ckeditor/ckeditor5-basic-styles/src/bold";
import ItalicPlugin from "@ckeditor/ckeditor5-basic-styles/src/italic";
import UnderlinePlugin from "@ckeditor/ckeditor5-basic-styles/src/underline";
import StrikethroughPlugin from "@ckeditor/ckeditor5-basic-styles/src/strikethrough";
import CodePlugin from "@ckeditor/ckeditor5-basic-styles/src/code";
import SubscriptPlugin from "@ckeditor/ckeditor5-basic-styles/src/subscript";
import SuperscriptPlugin from "@ckeditor/ckeditor5-basic-styles/src/superscript";
import LinkPlugin from "@ckeditor/ckeditor5-link/src/link";
import ParagraphPlugin from "@ckeditor/ckeditor5-paragraph/src/paragraph";
import EasyImage from "@ckeditor/ckeditor5-easy-image/src/easyimage";
import Font from "@ckeditor/ckeditor5-font/src/font";
import Heading from "@ckeditor/ckeditor5-heading/src/heading";
import HeadingButtonsUI from "@ckeditor/ckeditor5-heading/src/headingbuttonsui";
import Highlight from "@ckeditor/ckeditor5-highlight/src/highlight";

export default defineComponent({
  setup() {
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listStatus = gpf.dataStatus();

    const titleTable: Array<string> = [
      "Banner",
      "Title",
      "Blog Kategori",
      "Blog Tag",
      "Status",
      "Created",
      "#",
    ];

    // "id": 1,
    // "slug": "cpnsonlineid-penipuan-simak-kebenarannya-berikut-ini",
    // "title": "CPNSONLINE.ID Penipuan? Simak Kebenarannya Berikut ini!",
    // "banner": "https://api.cpnsonline.id/img/aff2c81a0d525aed56c4afb52ce8a587.png",
    // "isActive": true,
    // "createdAt": "2021-04-21 22:32:06",
    // "BlogCategories": [
    //     {
    //         "id": 1,
    //         "title": "Info CPNS"
    //     },
    //     {
    //         "id": 2,
    //         "title": "Info PPPK"
    //     }
    // ],
    // "BlogTags": [
    //     {
    //         "id": 1,
    //         "title": "CPNS"
    //     },
    //     {
    //         "id": 2,
    //         "title": "PPPK"
    //     }
    // ]

    const dataBA = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingBAadmin").show();

      const hide = () => cash("#loadingBAadmin").hide();
      const page = dataBA.page;
      const pageSize = dataBA.pageSize;
      const search = dataBA.search;
      const url = api.adminBlogArtikel +
        "?search=" +
        search +
        "&page=" +
        page +
        "&pageSize=" +
        pageSize;

      const hitApi = await api.hitApiGet(url, hide);
      const ds = hitApi.data.diagnostic;
      const rs = hitApi.data.response;
      // console.log("response get du ", rs);

      dataBA.data = rs;
      dataBA.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);

    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataBA.page = p;
      getData();
    };

    const nextPage = () => {
      dataBA.page = dataBA.page + 1;
      getData();
    };

    const beforePage = () => {
      dataBA.page = dataBA.page - 1;
      getData();
    };

    const lastPage = () => {
      dataBA.page = dataBA.paginate.totalPages;
      getData();
    };

    // BEGIN for CKEditor
    const date = ref("");
    const classicEditor = ClassicEditor;
    const inlineEditor = InlineEditor;
    const balloonEditor = BalloonEditor;
    const documentEditor = DocumentEditor;
    const simpleEditorConfig = {
      plugins: [
        ParagraphPlugin,
        BoldPlugin,
        UnderlinePlugin,
        ItalicPlugin,
        LinkPlugin,
      ],
      toolbar: {
        items: ["bold", "italic", "underline", "link"],
      },
    };
    const editorConfig = {
      cloudServices: {
        tokenUrl: "",
        uploadUrl: "",
      },
      plugins: [
        Font,
        EssentialsPlugin,
        BoldPlugin,
        UnderlinePlugin,
        StrikethroughPlugin,
        ItalicPlugin,
        LinkPlugin,
        ParagraphPlugin,
        CodePlugin,
        SubscriptPlugin,
        SuperscriptPlugin,
        EasyImage,
        Heading,
        HeadingButtonsUI,
        Highlight,
      ],
      toolbar: {
        items: [
          "fontSize",
          "fontFamily",
          "fontColor",
          "fontBackgroundColor",
          "bold",
          "italic",
          "underline",
          "strikethrough",
          "code",
          "subscript",
          "superscript",
          "link",
          "undo",
          "redo",
          "imageUpload",
          "highlight",
        ],
      },
    };
    const editorData = ref("<p>Content of the editor.</p>");
    const initDocumentEditor = (editor: any) => {
      const toolbarContainer = document.querySelector(
        ".document-editor__toolbar"
      );
      toolbarContainer?.appendChild(editor.ui.view.toolbar.element);
      const win: any = window;
      win.editor = editor;
    };
    // END for CKEditor

    return {
      titleTable,
      getData,
      dataBA,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listStatus,

      // for CKEditor
      date,
      classicEditor,
      inlineEditor,
      balloonEditor,
      documentEditor,
      simpleEditorConfig,
      editorConfig,
      editorData,
      initDocumentEditor,
    };
  },
  data() {
    return {
      // for modal data bs
      section: "add",

      exDataBA: {
        id: 0,
        title: "",
        keyword: "",
        description: "",
        content: "",
        status: "1",
        kategori: [],
        tag: [],
        banner: "",
        nameBanner: "Choose File",
        oversizeBanner: false,
        tempBanner: "",
      },

      exDeleteBA: {
        id: 0,
        title: "",
      },

      listKategori: [],
      listTag: [],
    };
  },
  methods: {
    changeTS(v: any, f = "") {
      console.log("v multiple ", v);
      console.log("exDataBA ", this.exDataBA);

      const val = v.detail.args[1].key;
      const ex: any = this.exDataBA;
      ex[f] = val;
    },
    chooseBanner() {
      const id = "fileBannerDM";
      const btn: any = document.getElementById(id);
      btn.click();
    },
    removeBanner() {
      const ex = this.exDataBA;
      ex.nameBanner = "Choose File";
      ex.banner = "";
      cash("#showImage").attr("src", "");
    },
    handleFile(evt: any ) {
      const target = evt.target;
      const files = target.files;
      const sizeFiles = target.files[0].size;
      const ex = this.exDataBA;

      if (sizeFiles > 1000000) {
        ex.oversizeBanner = true;
        this.removeBanner();
      } else {
        const nameFile = target.files[0].name;
        ex.nameBanner = nameFile;
        ex.banner = files[0];
        ex.oversizeBanner = false;
        // const bodyFormData = new FormData();
        // bodyFormData.append("files", files[0]);

        this.renderImage(target.files[0]);
      }
      // console.log("evt ", evt);
    },
    renderImage(files: any) {
      // console.log('masuk render image ', files)
      const reader = new FileReader();
      reader.onload = function(e: any) {
        cash("#showImage").attr("src", e.target.result);
      };
      reader.readAsDataURL(files);
    },
    async emptyField() {
      const ex = this.exDataBA;
      ex.title = "";
      ex.status = "1";
      ex.keyword = "";
      ex.description = "";
      ex.content = "";
      ex.banner = "";
      ex.status = "1";
      ex.kategori = [];
      ex.tag = [];
    },
    async callKategoriTag() {
      gpf.gLoading.show();

      const urlKategori = api.adminBlogKategori;
      const hitApiKategori = await api.hitApiGet(urlKategori);
      const rsKategori = hitApiKategori.data.response;
      this.listKategori = rsKategori;

      const urlTag = api.adminBlogTag;
      const hitApiTag = await api.hitApiGet(urlTag);
      const rsTag = hitApiTag.data.response;
      this.listTag = rsTag;

      gpf.gLoading.hide();
    },
    async addBA() {
      await this.emptyField();
      await this.callKategoriTag();

      this.section = "add";
      gpf.showModal("#modal-data-ba");
    },
    async addBAApi() {
      gpf.gLoading.show();
      const hide = () => gpf.gLoading.hide();
      const ex = this.exDataBA;

      console.log('addBAApi ', ex);

      const params = new FormData();
      params.append("title", ex.title);
      params.append("keyword", ex.keyword);
      params.append("description", ex.description);
      params.append("content", ex.content);
      params.append("status", ex.status);
      params.append("banner", ex.banner);

      for (let i = 0; i < ex.kategori.length; i++) {
        params.append("kategori[]", ex.kategori[i]);
      }

      for (let i = 0; i < ex.tag.length; i++) {
        params.append("tag[]", ex.tag[i]);
      }

      const url = api.adminBlogArtikel;
      const hitApi = await api.hitApiPost(url, params, hide);
      await gpf.handleSds(hitApi);
      await gpf.hideModal("#modal-data-ba");
      await this.emptyField();
      await this.getData();

    },
    async editBA(data: any) {
      this.section = "edit";

      await this.callKategoriTag();

      const urlBA = api.adminBlogArtikel + data.id;
      const hitApi = await api.hitApiGet(urlBA);
      const rs = hitApi.data.response;

      const ex = this.exDataBA;
      ex.id = data.id;
      ex.title = rs.title;
      ex.keyword = rs.keyword ? rs.keyword : "";
      ex.description = rs.description ? rs.description : "";
      ex.content = rs.content ? rs.content : "";
      ex.banner = rs.banner;
      ex.status = rs.isActive ? "1" : "0";

      ex.kategori = rs.BlogCategories.map((e: any) => {
        return e.id.toString();
      });
      ex.tag = rs.BlogTags.map((e: any) => {
        return e.id.toString();
      });

      gpf.showModal("#modal-data-ba");
    },
    async editBAApi() {
      gpf.gLoading.show();
      const hide = () => gpf.gLoading.hide();
      const ex = this.exDataBA;
      const params = new FormData();
      params.append("title", ex.title);
      params.append("keyword", ex.keyword);
      params.append("description", ex.description);
      params.append("content", ex.content);
      params.append("status", ex.status);
      params.append("banner", ex.banner);

      for (let i = 0; i < ex.kategori.length; i++) {
        params.append("kategori[]", ex.kategori[i]);
      }

      for (let i = 0; i < ex.tag.length; i++) {
        params.append("tag[]", ex.tag[i]);
      }

      const url = api.adminBlogArtikel + ex.id;
      const hitApi = await api.hitApiPut(url, params, hide);
      await gpf.handleSds(hitApi);
      await gpf.hideModal("#modal-data-ba");
      await this.emptyField();
      await this.getData();
    },
    deleteBA(data: any): void {
      const ex = this.exDeleteBA;
      ex.id = data.id;
      ex.title = data.title;

      gpf.showModal("#delete-modal-data-ba");
    },
    async deleteBAApi() {
      gpf.gLoading.show();

      const hide = () => gpf.gLoading.hide();
      const ex = this.exDeleteBA;

      const url = api.adminBlogArtikel + ex.id;
      const hitApi = await api.hitApiDel(url, hide);
      ex.id = 0;
      ex.title = "";

      await gpf.handleSds(hitApi);
      await gpf.hideModal("#delete-modal-data-ba");
      await this.getData();
    }
  },
});
</script>
