<template>
  <div class="grid grid-cols-12 gap-6">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex items-center h-10">
          <h2 class="text-lg font-medium truncate mr-5">Materi Penunjang</h2>
          <a
            href="javascript:;"
            class="ml-auto flex text-theme-1 dark:text-theme-10"
            @click="getData()"
          >
            <RefreshCcwIcon class="w-4 h-4 mr-3" />
            <span class="hidden md:block">Reload Data</span>
          </a>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div v-if="role === 'member'" class="col-span-12 grid grid-cols-8 gap-2">
      <div
        class="intro-y flex flex-col md:flex-row items-center col-span-8 md:col-span-3"
      >
        <p
          class="text-base mr-auto md:mr-2 mb-3 md:mb-0 whitespace-nowrap text-left"
        >
          Filter :
        </p>
        <div class="w-full flex flex-row">
          <TailSelect
            v-model="dataMP.jenis"
            v-on:change="changeTS($event, 'jenis')"
            :options="{
              classNames: 'w-full',
            }"
          >
            <option
              v-for="(s, idx) in dataMP.listJenis"
              v-bind:key="idx"
              :value="s.id"
            >
              {{ s.title }}
            </option>
          </TailSelect>
          <button
            @click="getData()"
            class="button bg-theme-1 hover:bg-primary text-white w-24 ml-2"
          >
            View
          </button>
        </div>
      </div>
      <div class="intro-y w-full col-span-8 md:col-span-5">
        <div
          class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
        >
          <input
            type="text"
            class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
            placeholder="Search..."
            v-model="dataMP.search"
            v-on:keyup.enter="getData()"
          />
          <SearchIcon
            @click="getData()"
            class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0 cursor-pointer"
          />
        </div>
      </div>
    </div>
    <div class="col-span-12 lg:col-span-12 xxl:col-span-12">
      <!-- BEGIN: File Manager Filter -->
      <div
        v-if="role !== 'member'"
        class="intro-y flex flex-col-reverse sm:flex-row items-center"
      >
        <div class="w-full sm:w-auto relative ml-auto sm:mt-0">
          <SearchIcon
            class="w-4 h-4 absolute my-auto inset-y-0 ml-3 left-0 z-10 text-gray-700 dark:text-gray-300"
          />
          <input
            type="text"
            class="input w-full sm:w-80 box pl-10 text-gray-700 dark:text-gray-300"
            placeholder="Search files"
            v-model="dataMP.search"
            v-on:keyup.enter="getData()"
          />
        </div>
      </div>
      <!-- END: File Manager Filter -->
      <!-- BEGIN: Directory & Files -->
      <LoadingDiv id="loadingMP" />
      <div class="intro-y grid grid-cols-12 gap-3 sm:gap-6 mt-5">
        <UrlSlug
          v-for="(p, idx) in dataMP.data"
          :key="idx"
          :to="`materi-penunjang/${p.slug}`"
          class="intro-y col-span-6 sm:col-span-4 md:col-span-3 xxl:col-span-2"
          :title="p.title"
        >
          <div
            class="file box rounded-md pt-7 pb-5 px-3 sm:px-5 relative zoom-in"
          >
            <UrlSlug :to="`materi-penunjang/${p.slug}`" class="w-3/5 mx-auto">
              <img
                class="w-full"
                :src="p.banner"
                alt="banner-materi"
                :title="'Banner ' + p.banner"
              />
            </UrlSlug>
            <UrlSlug
              :to="`materi-penunjang/${p.slug}`"
              class="block font-medium mt-4 text-center truncate"
              :title="p.title"
            >
              {{ p.title }}
            </UrlSlug>
          </div>
        </UrlSlug>
      </div>
      <div
        v-if="dataMP.data.length === 0"
        class="w-full p-5 flex items-center justify-center"
      >
        <p class="text-base font-bold">Tidak Ada Data Materi.</p>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataMP.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataMP.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataMP.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataMP.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataMP.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataMP.page !== dataMP.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataMP.page !== dataMP.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataMP.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  components: {},
  setup() {
    const role = api.getRole();
    const dataMP = reactive({
      data: [] as any,
      page: 1,
      pageSize: 12,
      search: "",
      jenis: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      listJenis: [] as any,
    });

    const getData = async () => {
      cash("#loadingMP").show();

      const hide = () => cash("#loadingMP").hide();
      const page = dataMP.page;
      const pageSize = dataMP.pageSize;
      const search = dataMP.search;
      const jenis = dataMP.jenis;
      const url =
        (await api.getConfigFeatureMember("memberMateri", "featureMateri")) +
        "pdf/" +
        "?search=" +
        search +
        "&page=" +
        page +
        "&pageSize=" +
        pageSize +
        (role === "member" ? "&jenis=" + jenis : "");

      console.log("role url ", role, url);

      const hitApi = await api.hitApiGet(url, hide);
      const ds = hitApi.data.diagnostic;
      const rs = hitApi.data.response;

      dataMP.data = rs;
      dataMP.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
    };

    const getJenisMateri = async () => {
      gpf.gLoading.show();

      const hide = () => gpf.gLoading.hide();
      const url = api.memberMateriJenis;
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;
      dataMP.listJenis = rs;
      dataMP.jenis = rs[0].id;
    };

    const hitFirst = async () => {
      if (role === "member") {
        await getJenisMateri();
        await getData();
      } else {
        await getData();
      }
    };
    hitFirst();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataMP.page = p;
      getData();
    };

    const nextPage = () => {
      dataMP.page = dataMP.page + 1;
      getData();
    };

    const beforePage = () => {
      dataMP.page = dataMP.page - 1;
      getData();
    };

    const lastPage = () => {
      dataMP.page = dataMP.paginate.totalPages;
      getData();
    };

    return {
      getData,
      dataMP,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      role,
    };
  },
  methods: {
    changeTS(v: any, f: any) {
      const val = v.detail.args[1].key;
      const ex: any = this.dataMP;
      ex[f] = val;
    },
  },
});
</script>
