<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data Marketing Kit</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="addMK()"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataMK.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                @click="getData()"
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0 cursor-pointer"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataMK.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <span class="max-w-4/12 truncate">{{ u.title }}</span>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <div class="flex flex-row max-w-10/12 truncate items-center">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Copy Link"
                      @click="$gpf.copy(u.content)"
                    >
                      <CopyIcon
                        class="w-6 h-6 cursor-pointer text-primary"
                      />
                    </Tippy>
                    <a :href="u.content" target="_blank" class="truncate">{{ u.content }}</a>
                  </div>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <span class="max-w-4/12 truncate">{{ u.isActive === 1 ? "Active" : "Suspend"}}</span>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editMK(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                      @click="deleteMK(u)"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer text-theme-6"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataMK.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Marketing Kit.</p>
          </div>
          <LoadingDiv id="loadingDataMK" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataMK.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataMK.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataMK.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataMK.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataMK.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataMK.page !== dataMK.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataMK.page !== dataMK.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataMK.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

    <!-- Modal Add and Edit Marketing Kit -->
    <div id="modal-data-mk" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Marketing Kit" : "Edit Marketing Kit" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-mk')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div 
            class="flex flex-col sm:flex-row items-center"
          >
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Title</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Title"
              v-model="exDataMK.title"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Content</label>
            <input
              type="email"
              class="input w-full border flex-1"
              placeholder="Link"
              v-model="exDataMK.content"
            />
          </div>
					<div 
						class="flex flex-col sm:flex-row items-center mt-3"
					>
            <label
              class="w-full whitespace-nowrap sm:w-20 sm:text-right sm:mr-5"
              >Status</label
            >
            <div class="w-1/2">
              <TailSelect
                v-model="exDataMK.active"
                v-on:change="changeTS($event, 'active')"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listStatus"
                  v-bind:key="idx"
                  :value="s.value"
                >
                  {{ s.title }}
                </option>
              </TailSelect>
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-mk')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="addMKApi()"
            v-if="section === 'add'"
          >
            Simpan
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="editMKApi()"
            v-if="section === 'edit'"
          >
            Update
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Delete Marketing Kit -->
    <div id="delete-modal-data-mk" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Title : "{{ exDeleteMK.title }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-mk')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="deleteMKApi()"
          >
            Delete
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { helper as $h} from "@/utils/helper";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listStatus = gpf.dataStatus();

    const titleTable: Array<string> = [
      "Title",
      "Content",
      "Status",
    ];

    const dataMK = reactive({
      tempData: [] as any,
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
        dataPaginate: [],
      } as any,
    });

    const getDataPagination = async () => {
      const page = dataMK.page;
      const pageSize = dataMK.pageSize;
      dataMK.paginate = $h.paginationLocal(dataMK.tempData.length, page, pageSize, 3, dataMK.tempData);
      dataMK.data = dataMK.paginate.dataPaginate;
    };

    const getData = async () => {
      cash("#loadingDataMK").show();

      const url = api.adminMarketingKit;
      const hide = () => cash("#loadingDataMK").hide();
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;

      dataMK.tempData = rs;
      await getDataPagination();
      
    };
    getData();

    const changePS = () => {
      getDataPagination();
    };

    const goToPage = (p: number) => {
      dataMK.page = p;
      getDataPagination();
    };

    const nextPage = () => {
      dataMK.page = dataMK.page + 1;
      getDataPagination();
    };

    const beforePage = () => {
      dataMK.page = dataMK.page - 1;
      getDataPagination();
    };

    const lastPage = () => {
      dataMK.page = dataMK.paginate.totalPages;
      getDataPagination();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataMK,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listStatus,
    };
  },
  data() {
    return {
      section: "add",
      exDataMK: {
        id: "",
        title: "",
        content: "",
        active: "1",
      },
      exDeleteMK: {
        id: "",
        title: "",
      },
    };
  },
  methods: {
    changeTS(v: any, f: any) {
      const val = v.detail.args[1].key;
      const ex: any = this.exDataMK;
      ex[f] = val;
    },
    async emptyField() {
      const ex = this.exDataMK;
      ex.id = "";
      ex.title = "";
      ex.content = "";
      ex.active = "1";
    },
    async addMK() {
      this.section = "add";
      await this.emptyField();
      gpf.showModal("#modal-data-mk");
    },
    async addMKApi() {
      gpf.gLoading.show();
      const ex = this.exDataMK;
      const params = new URLSearchParams();
      params.append("title", ex.title);
      params.append("content", ex.content);
      params.append("active", ex.active);

      const hide = () => gpf.gLoading.hide();
      const url = api.adminMarketingKit;
      const hitApi = await api.hitApiPost(url, params, hide);
      await gpf.handleSds(hitApi);
      gpf.hideModal("#modal-data-mk");
      await this.getData();
      await this.emptyField();

    },
    async editMK(data: any) {
      this.section = "edit";
      const ex = this.exDataMK;
      ex.id = data.id;
      ex.title = data.title;
      ex.content = data.content;
      ex.active = data.isActive.toString();
      gpf.showModal("#modal-data-mk");
    },
    async editMKApi() {
      gpf.gLoading.show();
      const ex = this.exDataMK;
      const params = new URLSearchParams();
      params.append("title", ex.title);
      params.append("content", ex.content);
      params.append("active", ex.active);

      const hide = () => gpf.gLoading.hide();
      const url = api.adminMarketingKit;
      const hitApi = await api.hitApiPut(url + ex.id, params, hide);
      gpf.hideModal("#modal-data-mk");
      await gpf.handleSds(hitApi);
      await this.getData();
      await this.emptyField();
    },
    async deleteMK(data: any) {
      const ex = this.exDeleteMK;
      ex.id = data.id;
      ex.title = data.title;
      gpf.showModal("#delete-modal-data-mk");
    },
    async deleteMKApi() {
      gpf.gLoading.show();
      const ex = this.exDeleteMK;

      const hide = () => gpf.gLoading.hide();
      const url = api.adminMarketingKit;
      const hitApi = await api.hitApiDel(url + ex.id, hide);
      await gpf.handleSds(hitApi);
      await this.getData();

      gpf.hideModal("#delete-modal-data-mk");
    },
  },
});
</script>
