
import { defineComponent, reactive } from "vue";
import { api } from "@/utils/api-config";
import { gpf } from "@/utils/global-functions";
import { VueCookieNext as $ck } from "vue-cookie-next";

export default defineComponent({
  setup() {
		const ld = api.getLoginData();
		const dataProfile = reactive({
      data: {
        id: ld.id,
        username: ld.username,
        email: ld.email,
        name: ld.name,
        phone: ld.phone,
        photo: ld.photo,
        role: ld.role,
        token: ld.token,
      },
			emailLupaPassword: "",
			ubahPassword: {
				oldPassword: "",
				newPassword: "",
			},
      ubahPin: {
				oldPin: "",
				newPin: "",
			},
		});

		const getDataProfile = async () => {
			cash("#loadingDP").show();
			const hide = () => cash("#loadingDP").hide();

			const url = api.publicProfile;
			const hitApi = await api.hitApiGet(url, hide);
			const rs = hitApi.data.response;
			const ex = dataProfile.data;
			ex.username = rs.username;
			ex.email = rs.email;
			ex.name = rs.name;
			ex.phone = rs.phone;
			ex.photo = rs.photo;

      $ck.setCookie("login-data", ex);
		};
		getDataProfile();

		const modalLupaPassword = () => {
			dataProfile.emailLupaPassword = ''; 
			gpf.showModal('#modal-lupa-password-profile');
		}

		const requestLupaPassword = async () => {
			gpf.gLoading.show();
			const hide = () => gpf.gLoading.hide();

			const params = new URLSearchParams();
      params.append("email", dataProfile.emailLupaPassword);

			const url = api.publicForgotPassword;
			const hitApi = await api.hitApiPut(url, params, hide);
			gpf.hideModal('#modal-lupa-password-profile');
			gpf.handleSds(hitApi, 'toast');

		};

		const modalUbahPassword = () => {
			const ex = dataProfile.ubahPassword;
			ex.oldPassword = ''; 
			ex.newPassword = ''; 
			gpf.showModal('#modal-ubah-password');
		}

		const requestUbahPassword = async () => {
			gpf.gLoading.show();
			const hide = () => gpf.gLoading.hide();

			const ex = dataProfile.ubahPassword;
			const params = new URLSearchParams();
      params.append("oldPassword", ex.oldPassword);
      params.append("newPassword", ex.newPassword);

			const url = api.publicResetPassword;
			const hitApi = await api.hitApiPut(url, params, hide);
			gpf.hideModal('#modal-ubah-password');
			gpf.handleSds(hitApi, 'toast');

		};

    const modalUbahPin = () => {
			const ex = dataProfile.ubahPin;
			ex.oldPin = ''; 
			ex.newPin = ''; 
			gpf.showModal('#modal-ubah-pin');
		}

    const requestUbahPin = async () => {
			gpf.gLoading.show();
			const hide = () => gpf.gLoading.hide();

			const ex = dataProfile.ubahPin;
			const params = new URLSearchParams();
      params.append("oldPin", ex.oldPin);
      params.append("newPin", ex.newPin);

			const url = api.publicResetPin;
			const hitApi = await api.hitApiPut(url, params, hide);
			gpf.hideModal('#modal-ubah-pin');
			gpf.handleSds(hitApi, 'toast');

		};

    return {
			dataProfile,
			modalLupaPassword,
			requestLupaPassword,
			modalUbahPassword,
			requestUbahPassword,
      modalUbahPin,
      requestUbahPin,
      getDataProfile,
    };
  },
  data() {
    return {
      exDataUP: {
        name: "",
        phone: "",
        email: "",
        photo: "",
        nameBanner: "Choose File",
        oversizeBanner: false,
        tempBanner: "",
      }
    }
  },
  methods: {
    chooseBanner() {
      const id = "fileBannerDM";
      const btn: any = document.getElementById(id);
      btn.click();
    },
    removeBanner() {
      const ex = this.exDataUP;
      ex.nameBanner = "Choose File";
      ex.photo = "";
      cash("#showImage").attr("src", "");
    },
    handleFile(evt: any ) {
      const target = evt.target;
      const files = target.files;
      const sizeFiles = target.files[0].size;
      const ex = this.exDataUP;

      if (sizeFiles > 1000000) {
        ex.oversizeBanner = true;
        this.removeBanner();
      } else {
        const nameFile = target.files[0].name;
        ex.nameBanner = nameFile;
        ex.photo = files[0];
        ex.oversizeBanner = false;
        // const bodyFormData = new FormData();
        // bodyFormData.append("files", files[0]);

        this.renderImage(target.files[0]);
      }
      // console.log("evt ", evt);
    },
    renderImage(files: any) {
      // console.log('masuk render image ', files)
      const reader = new FileReader();
      reader.onload = function(e: any) {
        cash("#showImage").attr("src", e.target.result);
      };
      reader.readAsDataURL(files);
    },
    async emptyFieldUP() {
      const dp = this.dataProfile.data;
      const ex = this.exDataUP;
      ex.name = dp.name;
      ex.phone = dp.phone;
      ex.email = dp.email;
      ex.photo = dp.photo;
      ex.nameBanner = "Choose File";
      ex.oversizeBanner = false;
      ex.tempBanner = "";
      this.removeBanner();
    },
    async modalUpdateProfile() {
      await this.emptyFieldUP();
      await gpf.showModal("#modal-update-profile");
    },
    async updateProfileApi() {
      gpf.gLoading.show();

      const hide = () => gpf.gLoading.hide();
      const ex = this.exDataUP;
      const body = new FormData();
      body.append("name", ex.name);
      body.append("phone", ex.phone);
      body.append("email", ex.email);
      body.append("photo", ex.photo);

      const url = api.publicProfile;
      const hitApi = await api.hitApiPut(url, body, hide);
      await gpf.handleSds(hitApi, 'toast');
      await gpf.hideModal("#modal-update-profile");
      await this.emptyFieldUP();
      await this.getDataProfile();

    }
  }
});
