<template>
  <div class="intro-y flex flex-col sm:flex-row items-center mt-8">
    <h2 class="text-lg font-medium mr-auto"></h2>
    <div class="w-full sm:w-auto flex mt-4 sm:mt-0">
      <a
        href="https://cpnsonline.id/konfirmasi"
        target="_blank"
        class="button text-white bg-theme-1 shadow-md ml-auto"
        >Konfirmasi Pembayaran</a
      >
    </div>
  </div>
  <LoadingDiv id="loadingInvoice" />
  <div 
    id="mungkinError"
    class="col-span-12 box p-5 flex justify-center items-center mt-5"
    style="display: none"
  >
    <h3 class="text-base">Maaf, Mungkin terjadi suatu kesalahan.</h3>
  </div>
  <div
    id="dataInvoice"
    class="intro-y box overflow-hidden mt-5"
    style="display: none"
  >
    <div
      class="border-b border-gray-200 dark:border-dark-5 text-center sm:text-left"
    >
      <div class="px-5 py-10 sm:px-20 sm:py-20">
        <div class="text-theme-1 dark:text-theme-10 font-semibold text-3xl">
          INVOICE
        </div>
        <div class="mt-2">
          Receipt <span class="font-medium">#{{ dataInv.data.invoice }}</span>
        </div>
        <div class="mt-1">{{ $h.formatDate(dataInv.data.createdAt, "DD MMM, YYYY HH:mm:ss") }}</div>
      </div>
      <div
        class="flex flex-col lg:flex-row px-5 sm:px-20 pt-10 pb-10 sm:pb-20"
      >
        <div class="">
          <div class="text-base text-gray-600">Billing to</div>
          <div
            class="text-lg font-medium text-theme-1 dark:text-theme-10 mt-2"
          >
            {{ dataInv.data.billingTo.name }}
          </div>
          <div class="mt-1">{{ dataInv.data.billingTo.email }}</div>
          <div class="mt-1">{{ dataInv.data.billingTo.phone }}</div>
        </div>
        <div class="lg:text-right mt-10 lg:mt-0 lg:ml-auto">
          <div class="text-base text-gray-600">Payment to</div>
          <div
            class="text-lg font-medium text-theme-1 dark:text-theme-10 mt-2"
          >
            {{ dataInv.data.paymentTo.name }}
          </div>
          <div class="mt-1">{{ dataInv.data.paymentTo.address }}</div>
          <div class="mt-1">{{ dataInv.data.paymentTo.phone }}</div>
        </div>
      </div>
    </div>
    <div class="px-5 sm:px-16 py-10 sm:py-20">
      <div class="overflow-x-auto">
        <table class="table">
          <thead>
            <tr>
              <th class="border-b-2 dark:border-dark-5 whitespace-nowrap">
                Paket Langganan
              </th>
              <th
                class="border-b-2 dark:border-dark-5 text-right whitespace-nowrap"
              >
                Harga
              </th>
              <th
                class="border-b-2 dark:border-dark-5 text-right whitespace-nowrap"
              >
                Diskon
              </th>
              <th
                class="border-b-2 dark:border-dark-5 text-right whitespace-nowrap"
              >
                Subtotal
              </th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(u, idx) in dataInv.data.items"
              :key="idx"
            >
              <td class="border-b dark:border-dark-5">
                <div class="font-medium whitespace-nowrap">
                  {{ u.paket }}
                </div>
              </td>
              <td class="text-right border-b dark:border-dark-5 w-32">
                {{ $gpf.convertToCurrency(u.price, 'IDR ') }}
              </td>
              <td class="text-right border-b dark:border-dark-5 w-32">
                {{ u.discount }}
              </td>
              <td
                class="text-right border-b dark:border-dark-5 w-32 font-medium"
              >
                {{ $gpf.convertToCurrency(u.subtotal, 'IDR ') }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div
      class="px-5 sm:px-20 pb-10 sm:pb-20 flex flex-col-reverse sm:flex-row"
    >
      <div class="text-center sm:text-left mt-10 sm:mt-0">
        <div class="text-base text-gray-600">
          {{ dataInv.data.paymentInfo.paymentType }}
        </div>
        <div class="text-lg text-theme-1 dark:text-theme-10 font-medium mt-2">
          {{ dataInv.data.paymentInfo.bankAccount }}
        </div>
        <div class="mt-1">{{ dataInv.data.paymentInfo.bankName }}</div>
        <div class="mt-1">{{ dataInv.data.paymentInfo.bankNumber }}</div>
      </div>
      <div class="text-center sm:text-right sm:ml-auto">
        <div class="text-base text-gray-600">Total Amount</div>
        <div class="text-xl text-theme-1 dark:text-theme-10 font-medium mt-2">
          {{ $gpf.convertToCurrency(dataInv.data.items[0].subtotal, 'IDR ') }}
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { useRoute } from "vue-router";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const route = useRoute();
    const slug: any = route.params.slug;
    const packageId = slug.split("-")[0];
    const paymentId = slug.split("-")[1];
    const promoId = slug.split("-")[2];

    const dataInv = reactive({
      data: {
        invoice: "210419BNCPHS",
        createdAt: "2021-04-20 00:12:40",
        billingTo: {
          name: "Test Member",
          email: "test@cpnsonline.id",
          phone: "6285340800358",
        },
        paymentTo: {
          name: "CPNSonline.id",
          address: "Gedung Megah Group - Jl. Adhyaksa Baru No.49",
          phone: "0811 460 0098",
        },
        items: [
          {
            paket: "Paket Platinum",
            price: 994000,
            discount: "50%",
            subtotal: 497000,
          },
        ],
        paymentInfo: {
          paymentType: "Virtual Account",
          bankAccount: "CPNSONLINE.ID/Test Member",
          bankName: "Bank Permata",
          bankNumber: "(013)-7178329835371324",
        },
      }
    })

    const getInvoice = async () => {
      cash("#loadingInvoice").show();
      cash("#dataInvoice").hide();
      cash("#mungkinError").hide();
      const hide = () => {
        cash("#loadingInvoice").hide();
      };

      const body = new URLSearchParams();
      body.append("packageId", packageId);
      body.append("paymentId", paymentId);
      body.append("promoId", promoId === "0" ? "" : promoId);

      const url = api.memberOrderPaket;
      const hitApi = await api.hitApiPost(url, body, hide);
      console.log("hitApi ", hitApi);

      if ( hitApi.status !== 200 ) {
        cash("#mungkinError").show();
      } else {
        cash("#dataInvoice").show();
        const rs = hitApi.data.response;
        dataInv.data = rs;
      }

    };
    getInvoice();

    return {
      dataInv,
    };
  },
});
</script>
