<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data Withdrawal Admin</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>

    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          ></div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataAW.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                @click="getData()"
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0 cursor-pointer"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                  v-html="k"
                ></th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataAW.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.name }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $gpf.convertToCurrency(u.saldo === null ? "0" : u.saldo, "IDR ")  }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $gpf.convertToCurrency(u.nominal, "IDR ") }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.bank }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.accountName }} <br />
                  {{ u.accountNumber }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.status }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.date, "DD MMM, YYYY HH:mm:ss") }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row justify-center items-center">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Accept Withdrawal"
                      @click="acceptAW(u)"
                      v-if="u.status !== 'paid'"
                    >
                      <CheckCircleIcon
                        class="w-6 h-6 cursor-pointer text-theme-9"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                      @click="deleteAW(u)"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer text-theme-6"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataAW.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Withdrawal.</p>
          </div>
          <LoadingDiv id="loadingAW" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataAW.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataAW.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataAW.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataAW.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataAW.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataAW.page !== dataAW.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataAW.page !== dataAW.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataAW.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

    <!-- Modal Accept Withdrawal -->
    <div id="modal-accept-aw" class="modal">
      <div class="min-h-screen flex justify-center items-center">
        <div class="box mx-auto w-auto">
          <div class="p-5 text-center">
            <CheckCircleIcon class="w-16 h-16 text-theme-9 mx-auto mt-3" />
            <div class="text-3xl mt-5">Apakah Anda yakin?</div>
            <div class="text-gray-600 mt-2">
              Ingin mengkonfirmasi permintaan withdrawal dari "{{ exAcceptAW.name }}" telah di transfer?
            </div>
          </div>
          <div class="px-5 pb-8 text-center">
            <button
              type="button"
              data-dismiss="modal"
              class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
              @click="$gpf.hideModal('#modal-accept-aw')"
            >
              Cancel
            </button>
            <button
              type="button"
              class="button w-24 bg-theme-9 text-white"
              @click="acceptAWApi()"
            >
              Yakin
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal Delete Withdrawal -->
    <div id="delete-modal-data-aw" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Apakah Anda yakin?</div>
          <div class="text-gray-600 mt-2">
            Ingin menghapus data dengan Nama : "{{ exDeleteAW.name }}"".
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gpf.hideModal('#delete-modal-data-aw')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="deleteAWApi()"
          >
            Delete
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();

    const titleTable: Array<string> = [
      "Nama",
      "Saldo",
      "Nominal",
      "Bank Name",
      "Account Name / <br> Account Number",
      "Status",
      "Created",
      "#",
    ];

    const dataAW = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingAW").show();

      const page = dataAW.page;
      const pageSize = dataAW.pageSize;
      const search = dataAW.search;
      const url =
        api.adminWithdrawal +
        "?search=" +
        search +
        "&page=" +
        page +
        "&pageSize=" +
        pageSize;

      const hide = () => cash("#loadingAW").hide();
      const hitApi = await api.hitApiGet(url, hide);
      const ds = hitApi.data.diagnostic;
      const rs = hitApi.data.response;

      dataAW.data = rs;
      dataAW.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);

    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataAW.page = p;
      getData();
    };

    const nextPage = () => {
      dataAW.page = dataAW.page + 1;
      getData();
    };

    const beforePage = () => {
      dataAW.page = dataAW.page - 1;
      getData();
    };

    const lastPage = () => {
      dataAW.page = dataAW.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataAW,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
    };
  },
  data() {
    return {
      exDataAW: {
        id: "",
        pin: "",
        amount: "",
        bank: "",
      },
      listBank: [],
      exAcceptAW: {
        id: "0",
        name: "",
      },
      exDeleteAW: {
        id: "0",
        name: "",
      },
    };
  },
  methods: {
    async acceptAW(data: any) {
      const ex = this.exAcceptAW;
      ex.id = data.id;
      ex.name = data.name;

      gpf.showModal("#modal-accept-aw");
    },
    async acceptAWApi() {
      gpf.gLoading.show();

      const hide = () => gpf.gLoading.hide();
      const ex = this.exAcceptAW;
      const params = new URLSearchParams();
      params.append("id", ex.id);

      const hitApi = await api.hitApiPost(api.adminWithdrawal, params, hide);
      await gpf.handleSds(hitApi);
      await this.getData();
    },
    deleteAW(data: any): void {
      const ex = this.exDeleteAW;
      ex.id = data.id;
      ex.name = data.name;

      gpf.showModal("#delete-modal-data-aw");
    },
    async deleteAWApi() {
      gpf.gLoading.show();

      const hide = () => gpf.gLoading.hide();
      const ex = this.exDeleteAW;
      const params = new URLSearchParams();
      params.append("id", ex.id);

      const hitApi = await api.hitApiDel(api.adminWithdrawal + ex.id, hide);
      await gpf.handleSds(hitApi);
      await gpf.hideModal("#delete-modal-data-aw");
      await this.getData();

    },
  },
});
</script>
