<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div
          class="intro-y flex flex-col md:flex-row items-center h-auto sm:h-10"
        >
          <h2 class="text-xl font-medium truncate">Status Garansi</h2>
          <div
            class="md:ml-auto mt-2 sm:mt-0 flex flex-row justify-center items-center"
          >
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="showKlaimCG('cpns');"
              :disabled="dataCG.btnKlaimCpns"
            >
              Klaim Garansi CPNS
            </button>
            <button 
              class="button bg-theme-1 hover:bg-primary text-white flex"
              :disabled="dataCG.btnKlaimPppk"
              @click="showKlaimCG('pppk');"
            >
              Klaim Garansi PPPK
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <LoadingDiv id="loadingCG" />
    <div id="listDataCG" class="col-span-12" style="display: none">
      <!-- BEGIN: Directory & Files -->
      <div
        v-for="(aa, idx) in dataCG.slugListData"
        :key="idx"
        class="intro-y col-span-12 box pt-3 md:pt-5 px-3 md:px-5 pb-5 mb-3"
      >
        <div class="flex flex-row mb-3">
          <h3 class="text-base font-medium">
            Hasil Tryout Paket {{ aa.title }}
          </h3>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataCG[aa.slug].histori"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.kode }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.totalSkor }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.status }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.tanggal, "DD MMM, YYYY HH:mm:ss") }}
                </td>
              </tr>
              <tr>
                <td
                  class="border-b-2 border-t-2 dark:border-dark-5 whitespace-nowrap"
                >
                  <div class="flex flex-col">
                    <p class="text-base font-semibold">Status :</p>
                    <p class="text-base">{{ dataCG[aa.slug].status }}</p>
                  </div>
                </td>
                <td
                  class="border-b-2 border-t-2 dark:border-dark-5 whitespace-nowrap"
                >
                  <div class="flex flex-col">
                    <p class="text-base font-semibold">Total CAT :</p>
                    <p class="text-base">{{ dataCG[aa.slug].totalCAT }}</p>
                  </div>
                </td>
                <td
                  class="border-b-2 border-t-2 dark:border-dark-5 whitespace-nowrap"
                >
                  <div class="flex flex-col">
                    <p class="text-base font-semibold">Average :</p>
                    <p class="text-base">{{ dataCG[aa.slug].average }}</p>
                  </div>
                </td>
                <td
                  class="border-b-2 border-t-2 dark:border-dark-5 whitespace-nowrap"
                >
                  <div class="flex flex-col">
                    <p class="text-base font-semibold">Passing Grade :</p>
                    <p class="text-base">{{ dataCG[aa.slug].passingGrade }}</p>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!-- END: Directory & Files -->
    </div>

    <!-- Modal Klaim Garansi -->
    <div id="modal-klaim-garansi" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3"
        >
          <h2 class="font-medium text-base mr-auto">
            Klaim Garansi
            <span class="uppercase">{{ exDataCG.jenis }}</span>
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-klaim-garansi')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col items-start">
            <label class="w-full font-semibold mb-1">Bank</label>
            <div class="min-w-32 w-10/12 flex flex-row">
              <TailSelect
                v-model="exDataCG.bankId"
                v-on:change="changeTS($event, 'bankId')"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(s, idx) in listBank"
                  v-bind:key="idx"
                  :value="s.id"
                >
                  {{ 
                    `${s.code} - ${s.accountNumber} - ${s.name} - ${s.accountName}`
                  }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col items-start mt-3">
            <label class="w-full font-semibold mb-1">Lampiran</label>
            <div class="flex flex-col w-10/12">
              <div class="flex items-center mb-2">
                <input
                  type="file"
                  class="button w-10 border flex-1 hidden"
                  placeholder="Lampiran"
                  id="fileBannerDM"
                  v-on:change="handleFile($event)"
                />
                <button
                  v-if="exDataCG.banner === ''"
                  @click="chooseBanner()"
                  class="button bg-theme-1 hover:bg-primary text-white w-28 whitespace-nowrap"
                >
                  Choose File
                </button>
                <button
                  v-else
                  @click="removeBanner()"
                  class="button bg-theme-1 hover:bg-primary text-white w-32 whitespace-nowrap"
                >
                  Remove
                </button>
                <span class="text-sm ml-2 truncate">{{ exDataCG.nameBanner }}</span>
              </div>
              <span class="text-sm">Note: Maks Ukuran File Lampiran = 1MB.</span>
              <span
                v-if="exDataCG.oversizeBanner"
                class="text-sm text-red-700"
              >Ukuran Lampiran melebihi 1MB, Silahkan Pilih Lampiran kembali.</span>
            </div>
          </div>
          <div class="flex flex-col items-center mt-3">
            <label class="w-full font-semibold mb-1">Preview Image</label>
            <div class="show-image w-80">
              <img
                id="showImage"
                src
                alt
              />
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-klaim-garansi')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="klaimGaransiApi()"
          >
            Klaim
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { api } from "@/utils/api-config";
import { gpf } from "@/utils/global-functions";

export default defineComponent({
  setup() {
    const titleTable: Array<string> = [
      "Kode",
      "Total Skor",
      "Status",
      "Tanggal",
    ];

    // kode: "2104151TR60V",
    // totalSkor: 139,
    // status: "Gagal",
    // tanggal: "2021-04-16 00:24:35",

    type listData = {
      status: "";
      totalCAT: 0;
      average: 0;
      passingGrade: 0;
      histori: [];
    };
    const dataCG = reactive({
      slugListData: [
        {
          title: "SKD",
          slug: "listSKD",
        },
        {
          title: "TKT",
          slug: "listTKT",
        },
        {
          title: "TBS",
          slug: "listTBS",
        },
        {
          title: "TMG",
          slug: "listTMG",
        },
        {
          title: "TSK",
          slug: "listTSK",
        },
      ],
      listSKD: {} as listData,
      listTKT: {} as listData,
      listTBS: {} as listData,
      listTMG: {} as listData,
      listTSK: {} as listData,
      btnKlaimCpns: true,
      btnKlaimPppk: true,
    });

    const getData = async () => {
      cash("#loadingCG").show();

      const hide = () => {
        cash("#loadingCG").hide();
        cash("#listDataCG").show();
      };
      const url = api.memberGaransi;
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;
      dataCG.listSKD = rs.skd;
      dataCG.listTKT = rs.tkt;
      dataCG.listTBS = rs.tbs;
      dataCG.listTMG = rs.tmg;
      dataCG.listTSK = rs.tsk;

      rs.skd.status === "Lulus"
        ? (dataCG.btnKlaimCpns = false)
        : (dataCG.btnKlaimCpns = true);
      rs.tkt.status === "Lulus" &&
      rs.tbs.status === "Lulus" &&
      rs.tmg.status === "Lulus" &&
      rs.tsk.status === "Lulus"
        ? (dataCG.btnKlaimPppk = false)
        : (dataCG.btnKlaimPppk = true);
    };
    getData();

    return {
      titleTable,
      dataCG,
    };
  },
  data() {
    return {
      exDataCG: {
        bankId: "",
        jenis: "",
        lampiran: "",
        banner: "",
        nameBanner: "Choose File",
        oversizeBanner: false,
        tempBanner: "",
      },
      listBank: [],
    };
  },
  methods: {
    changeTS(v: any, f: any) {
      const val = v.detail.args[1].key;
      const ex: any = this.exDataCG;
      ex[f] = val;
    },
    chooseBanner() {
      const id = "fileBannerDM";
      const btn: any = document.getElementById(id);
      btn.click();
    },
    removeBanner() {
      const ex = this.exDataCG;
      ex.nameBanner = "Choose File";
      ex.banner = "";
      cash("#showImage").attr("src", "");
    },
    handleFile(evt: any ) {
      const target = evt.target;
      const files = target.files;
      const sizeFiles = target.files[0].size;
      const ex = this.exDataCG;

      if (sizeFiles > 1000000) {
        ex.oversizeBanner = true;
        this.removeBanner();
      } else {
        const nameFile = target.files[0].name;
        ex.nameBanner = nameFile;
        ex.banner = files[0];
        ex.oversizeBanner = false;

        this.renderImage(target.files[0]);
      }
    },
    renderImage(files: any) {
      // console.log('masuk render image ', files)
      const reader = new FileReader();
      reader.onload = function(e: any) {
        cash("#showImage").attr("src", e.target.result);
      };
      reader.readAsDataURL(files);
    },
    async showKlaimCG(jenis: string) {
      gpf.gLoading.show();
      const ex = this.exDataCG;
      ex.jenis = jenis;

      const hide = () => gpf.gLoading.hide();
      this.listBank = await api.getAkunBank(hide);

      gpf.showModal("#modal-klaim-garansi");
    },
    async klaimGaransiApi() {
      gpf.gLoading.show();
      const hide = () => gpf.gLoading.hide();
      const ex = this.exDataCG;
      const body = new FormData();
      body.append("bankId", ex.bankId);
      body.append("jenis", ex.jenis);
      body.append("lampiran", ex.banner);

      const configGet = await api.configGet();
      const url = api.memberGaransi;
      const hitApi = await api.hitApiPost(url, body, hide, configGet);
      await gpf.handleSds(hitApi);

    }
  }
});
</script>
