<template>
  <DarkModeSwitcher />
  <div class="container sm:px-10">
    <div class="block xl:grid grid-cols-2 gap-4">
      <!-- BEGIN: Login Info -->
      <div class="hidden xl:flex flex-col min-h-screen">
        <a href="" class="-intro-x flex items-center pt-5">
          <img
            alt="Cpnsonline Tailwind HTML Admin Template"
            class="w-10 max-h-10"
            :src="require(`@/assets/images/cpnsonline-new-logo.png`)"
          />
          <span class="text-white text-lg ml-3">
            CPNS<span class="font-medium">Online</span>
          </span>
        </a>
        <div class="my-auto">
          <img
            alt="Cpnsonline Tailwind HTML Admin Template"
            class="-intro-x w-60 -mt-16"
            :src="'/img/owl-login.png'"
          />
          <div
            class="-intro-x text-white font-medium text-4xl leading-tight mt-10"
          >
            A few more clicks to <br />
            sign in to your account.
          </div>
          <!-- <div class="-intro-x mt-5 text-lg text-white dark:text-gray-500">
            Manage all your e-commerce accounts in one place
          </div> -->
        </div>
      </div>
      <!-- END: Login Info -->
      <!-- BEGIN: Login Form -->
      <div class="h-screen xl:h-auto flex py-5 xl:py-0 my-10 xl:my-0">
        <div
          class="my-auto mx-auto xl:ml-20 bg-white xl:bg-transparent px-5 sm:px-8 py-8 xl:p-0 rounded-md shadow-md xl:shadow-none w-full sm:w-3/4 lg:w-2/4 xl:w-auto"
        >
          <h2
            class="intro-x font-bold text-2xl xl:text-3xl text-center xl:text-left"
          >
            Sign In
          </h2>
          <div class="intro-x mt-2 text-gray-500 xl:hidden text-center">
            A few more clicks to sign in to your account. Manage all your
            e-commerce accounts in one place
          </div>
          <div class="intro-x mt-8">
            <input
              type="text"
              class="intro-x login__input input input--lg border border-gray-300 block"
              placeholder="Email"
              v-model="username"
              v-on:keyup.enter="login()"
            />
            <input
              type="password"
              class="intro-x login__input input input--lg border border-gray-300 block mt-4"
              placeholder="Password"
              v-model="password"
              v-on:keyup.enter="login()"
            />
          </div>
          <div
            class="intro-x flex text-gray-700 dark:text-gray-600 text-xs sm:text-sm mt-4"
          >
            <div class="flex items-center mr-auto">
              <!-- <input
                id="remember-me"
                type="checkbox"
                class="input border mr-2"
              />
              <label class="cursor-pointer select-none" for="remember-me"
                >Remember me</label
              > -->
            </div>
            <span 
              class="cursor-pointer"
              @click="modalLupaPassword();"
            >Lupa Password?</span>
          </div>
          <div class="intro-x mt-5 xl:mt-8 text-center xl:text-left">
            <button
              @click="login()"
              class="button button--lg w-full xl:w-32 text-white bg-theme-1 xl:mr-3 align-top"
            >
              Login
            </button>
            <a
              href="https://cpnsonline.id/"
            >
              <button
                class="button button--lg w-full xl:w-32 text-gray-700 border border-gray-300 dark:border-dark-5 dark:text-gray-300 mt-3 xl:mt-0 align-top"
              >
                Sign up
              </button>
            </a>
          </div>
          <div
            class="intro-x mt-10 xl:mt-24 text-gray-700 dark:text-gray-600 text-center xl:text-left"
          >
            By signin up, you agree to our <br />
            <a class="text-theme-1 dark:text-theme-10" href=""
              >Terms and Conditions</a
            >
            &
            <a class="text-theme-1 dark:text-theme-10" href=""
              >Privacy Policy</a
            >
          </div>
        </div>
      </div>
      <!-- END: Login Form -->
    </div>

    <!-- Modal Lupa Password -->
    <div id="modal-lupa-password-profile" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3"
        >
          <h2 class="font-medium text-base text-center mr-auto">
            Lupa Password
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-lupa-password-profile')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Email</label>
            <input
              type="email"
              class="input w-full border flex-1"
              placeholder="youremail@mail.com"
              v-model="dataProfile.emailLupaPassword"
							v-on:keyup.enter="requestLupaPassword()"
            />
          </div>
        </div>
        <div
          class="px-5 py-3 text-center"
        >
          <button
            type="button"
            class="button w-auto bg-theme-1 text-white"
            @click="requestLupaPassword()"
          >
            Request
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, reactive } from "vue";
import DarkModeSwitcher from "@/components/dark-mode-switcher/Main.vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";
import { useRouter } from "vue-router";
import { VueCookieNext as $ck } from "vue-cookie-next";

export default defineComponent({
  components: {
    DarkModeSwitcher,
  },
  setup() {
    const router = useRouter();
    const dataProfile = reactive({
			emailLupaPassword: "",
		});

    onMounted(() => {
      cash("body")
        .removeClass("app")
        .removeClass("error-page")
        .addClass("login");
    });

    const modalLupaPassword = () => {
			dataProfile.emailLupaPassword = ''; 
			gpf.showModal('#modal-lupa-password-profile');
		}

		const requestLupaPassword = async () => {
			gpf.gLoading.show();
			const hide = () => gpf.gLoading.hide();

			const params = new URLSearchParams();
      params.append("email", dataProfile.emailLupaPassword);

			const url = api.publicForgotPassword;
			const hitApi = await api.hitApiPut(url, params, hide);
			gpf.hideModal('#modal-lupa-password-profile');
			gpf.handleSds(hitApi);

		};

    return {
      router,
      dataProfile,
      modalLupaPassword,
      requestLupaPassword,
    };
  },
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    async login() {
      gpf.gLoading.show();

      const params = new URLSearchParams();
      params.append("username", this.username);
      params.append("password", this.password);
      const url = api.login;
      const config = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      };

      await axios
        .post(url, params, config)
        .then(async (result: any) => {
          // Do somthing
          // console.log('result login ', result);
          const rs = result.data.response;
          $ck.setCookie("login-data", rs);
          $ck.setCookie("tkn-data", rs.token);
          $ck.setCookie("role-data", rs.role);
          $ck.setCookie("rdm-nbr", gpf.randomNumbers(1, 999999, 5));
          await this.getListMenu();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();

          await gpf.handleEds(error);
        });
    },
    async getListMenu() {
      const config = api.configGet();
      axios
        .get(api.getMenu, config)
        .then(async (result: any) => {
          // console.log('response get menu ', result);
          const rs = result.data.response;
          $ck.setCookie("list-menu", JSON.stringify(rs));
          // console.log("menu cookie ", JSON.parse($ck.getCookie("list-menu")));
          await this.goToPageRole();
        })
        .catch(async (error: any) => {
          // console.log("response get menu ", error.response);
          gpf.gLoading.hide();

          await gpf.handleEds(error);
        });
    },
    async goToPageRole() {
      setTimeout(() => {
        gpf.gLoading.hide();
        this.$router.push("/" + api.getRole());
      }, 2000);
    }
  },
  beforeMount() {
    if ($ck.isCookieAvailable("login-data")) {
      this.$router.push("/" + api.getRole());
    }
  },
});
</script>
