<template>
  <div class="grid grid-cols-12 gap-3">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-3">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex items-center h-10">
          <h2 class="text-lg font-medium truncate mr-5">Dashboard Admin</h2>
          <a href="javascript:;" class="ml-auto flex text-theme-1 dark:text-theme-10" @click="getData();">
            <RefreshCcwIcon class="w-4 h-4 mr-3" /> <span class="hidden md:block">Reload Data</span>
          </a>
        </div>
        <div class="grid grid-cols-12 gap-3 mt-3">
          <div
            v-for="(li, idx) in dataAD.listAD"
            :key="idx"
            class="col-span-12 sm:col-span-6 xl:col-span-3 intro-y mb-3"
          >
            <div class="report-box zoom-in">
              <div class="box p-5">
                <div class="flex items-center">
                  <div>
                    <p class="text-base font-medium mr-2">{{ li.value }}</p>
                    <p>{{ li.title }}</p>
                  </div>
                  <div class="ml-auto">
                    <component :is="li.icon" :class="['w-10 h-10', ( 'text-' + li.color )] " />
                  </div>
                </div>
                <div class="w-full h-2 bg-gray-400 dark:bg-dark-1 rounded mt-3">
                  <div :class="['h-full rounded', ( 'w-' + li.percent ), ( 'bg-' + li.color )]"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="grid grid-cols-12 gap-2 mt-3">
          <!-- BEGIN: Line Chart -->
          <div class="col-span-12 sm:col-span-6 xxl:col-span-6 mb-1">
            <div class="intro-y box">
              <div
                class="flex flex-col sm:flex-row items-center p-5 border-b border-gray-200 dark:border-dark-5"
              >
                <h2 class="font-medium text-base mr-auto">Grafik Pendaftaran Member</h2>
              </div>
              <div id="line-chart" class="p-5">
                <div class="preview">
                  <LineChart :height="200" :data="dataAD.dataRC" />
                </div>
              </div>
            </div>
          </div>
          <!-- END: Line Chart -->
          <!-- BEGIN: Vertical Bar Chart -->
          <div class="col-span-12 sm:col-span-6 xxl:col-span-6 mb-1">
            <div class="intro-y box">
              <div
                class="flex flex-col sm:flex-row items-center p-5 border-b border-gray-200 dark:border-dark-5"
              >
                <h2 class="font-medium text-base mr-auto">Grafik Revenue</h2>
              </div>
              <div id="vertical-bar-chart" class="p-5">
                <div class="preview">
                  <VerticalBarChart :height="200" :data="dataAD.dataRV" />
                </div>
              </div>
            </div>
          </div>
          <!-- END: Vertical Bar Chart -->
        </div>
      </div>
      <!-- END: General Report -->
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { sk, tahapanBelajar, tryoutPemantapan, listTopDA } from "./dashboard-admin";
import VerticalBarChart from "@/components/vertical-bar-chart/Main.vue";
import LineChart from "@/components/line-chart/Main.vue";
import { api } from "@/utils/api-config";
import { gpf } from "@/utils/global-functions";

export default defineComponent({
  components: {
    VerticalBarChart,
    LineChart,
  },
  setup() {
    const dataAD = reactive({
      listAD: [] as any,
      dataRC: {} as any,
      dataRV: {} as any,
    })

    // get data widget dashboard
    const getDataAD = async () => {
      const url = api.adminDashboard;
      const hitApi = await api.hitApiGet(url);
      const rs = hitApi.data.response;
      dataAD.listAD = rs.map((e: any) => {
        e.value = gpf.ctc(e.value);
        e.percent = "4/5";
        e.color = `${e.color.split("-")[1]}-${e.color.split("-")[2]}`;
        return e;
      });
    };

    // get data grafik pendaftaran chart
    const getDataRC = async () => {
      const url = api.adminRegisterChart;
      const hitApi = await api.hitApiGet(url);
      const rs = hitApi.data.response;
      dataAD.dataRC = rs;
    };

    // get data grafik revenue chart
    const getDataRV = async () => {
      const url = api.adminRevenueChart;
      const hitApi = await api.hitApiGet(url);
      const rs = hitApi.data.response;
      dataAD.dataRV = rs;
    };

    const getData = async () => {
      gpf.gLoading.show();

      await getDataAD();
      await getDataRC();
      await getDataRV();

      gpf.gLoading.hide();
    };
    getData();

    return {
      dataAD,
      getData,
    };
  },
  data() {
    return {
      listSK: sk,
      listTB: tahapanBelajar,
      listTP: tryoutPemantapan,
      listDA: listTopDA,
    }
  }
});
</script>
