
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();

    const titleTable: Array<string> = [
			"Invoice",
      "Name",
      "Member",
      "Referral",
      "Amount",
      "Created",
    ];

    const dataDK = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      cash("#loadingDK").show();

      const page = dataDK.page;
      const pageSize = dataDK.pageSize;
      const search = dataDK.search;
      const url = api.adminDataKomisi +
        "?search=" +
        search +
        "&page=" +
        page +
        "&pageSize=" +
        pageSize;

      const hide = () => cash("#loadingDK").hide();
      const hitApi = await api.hitApiGet(url, hide);
      const ds = hitApi.data.diagnostic;
      const rs = hitApi.data.response;

      dataDK.data = rs;
      dataDK.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);

    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataDK.page = p;
      getData();
    };

    const nextPage = () => {
      dataDK.page = dataDK.page + 1;
      getData();
    };

    const beforePage = () => {
      dataDK.page = dataDK.page - 1;
      getData();
    };

    const lastPage = () => {
      dataDK.page = dataDK.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataDK,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
    };
  },
  data() {
    return {};
  },
  methods: {},
});
