<template>
  <div>
    <div class="intro-y flex items-center mt-6">
      <h2 class="text-lg font-medium mr-auto">Profile</h2>
    </div>
    <!-- BEGIN: Profile Info -->
    <div class="intro-y box px-5 pt-5 mt-5">
      <div
        class="flex flex-col lg:flex-row border-b border-gray-200 dark:border-dark-5 pb-5 -mx-5"
      >
        <div
          class="flex flex-1 px-5 items-center justify-center lg:justify-start"
        >
          <div
            class="w-20 h-20 sm:w-24 sm:h-24 flex-none lg:w-32 lg:h-32 image-fit relative"
          >
            <img
              alt="Midone Tailwind HTML Admin Template"
              class="rounded-full"
              :src="dataProfile.data.photo"
            />
          </div>
          <div class="ml-5 w-full">
            <div
              class="w-full truncate sm:whitespace-normal font-medium text-lg"
            >
              {{ dataProfile.data.name }}
            </div>
            <div class="text-gray-600">{{ dataProfile.data.role }}</div>
          </div>
        </div>
        <div
          class="mt-6 lg:mt-0 flex-1 dark:text-gray-300 px-5 border-l border-r border-gray-200 dark:border-dark-5 border-t lg:border-t-0 pt-5 lg:pt-0"
        >
          <div class="font-medium text-center lg:text-left lg:mt-5">
            Contact Details
          </div>
          <div
            class="flex flex-col justify-center items-center lg:items-start mt-4"
          >
            <div class="truncate sm:whitespace-normal flex items-center">
              <MailIcon class="w-4 h-4 mr-2" />
              {{ dataProfile.data.email }}
            </div>
            <div class="truncate sm:whitespace-normal flex items-center mt-3">
              <PhoneIcon class="w-4 h-4 mr-2" />
              {{ dataProfile.data.phone }}
            </div>
          </div>
        </div>
      </div>
      <div
        class="nav-tabs flex flex-col sm:flex-row justify-center lg:justify-start"
      >
        <a
          data-toggle="tab"
          data-target="#profile"
          href="javascript:;"
          class="py-4 sm:mr-8 flex items-center"
          @click="modalUpdateProfile()"
        >
          <UserIcon class="w-4 h-4 mr-2" /> Update Profile
        </a>
        <a
          data-toggle="tab"
          data-target="#account"
          href="javascript:;"
          class="py-4 sm:mr-8 flex items-center"
					@click="modalLupaPassword()"
        >
          <ShieldIcon class="w-4 h-4 mr-2" /> Lupa Password
        </a>
        <a
          data-toggle="tab"
          data-target="#change-password"
          href="javascript:;"
          class="py-4 sm:mr-8 flex items-center"
					@click="modalUbahPassword()"
        >
          <LockIcon class="w-4 h-4 mr-2" /> Ubah Password
        </a>
        <a
          data-toggle="tab"
          data-target="#settings"
          href="javascript:;"
          class="py-4 sm:mr-8 flex items-center"
          @click="modalUbahPin()"
        >
          <SettingsIcon class="w-4 h-4 mr-2" /> Ubah Pin
        </a>
      </div>
    </div>
    <!-- END: Profile Info -->
		<LoadingDiv id="loadingDP" />

		<!-- Modal Lupa Password -->
    <div id="modal-lupa-password-profile" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3"
        >
          <h2 class="font-medium text-base mr-auto">
            Lupa Password
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-lupa-password-profile')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Email</label>
            <input
              type="email"
              class="input w-full border flex-1"
              placeholder="Email"
              v-model="dataProfile.emailLupaPassword"
							v-on:keyup.enter="requestLupaPassword()"
            />
          </div>
        </div>
        <div
          class="px-5 py-3 text-center"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-lupa-password-profile')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-auto bg-theme-1 text-white"
            @click="requestLupaPassword()"
          >
            Request
          </button>
        </div>
      </div>
    </div>

		<!-- Modal Ubah Password -->
    <div id="modal-ubah-password" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3"
        >
          <h2 class="font-medium text-base mr-auto">
            Ubah Password
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-ubah-password')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col items-center mt-3 mx-5">
            <label class="w-full mb-1 ml-2">Password Sebelumnya</label>
            <div class="relative w-full">
              <input
                type="password"
                class="input pr-12 w-full border col-span-10"
                placeholder="Password"
                id="oldPassword"
                v-model="dataProfile.ubahPassword.oldPassword"
              />
              <div
                class="absolute top-0 right-0 rounded-r w-14 h-full flex items-center justify-center bg-gray-100 dark:bg-dark-1 dark:border-dark-4 border text-gray-600 cursor-pointer"
                @click="$gpf.showPass('oldPassword')"
              >
                show
              </div>
            </div>
          </div>
					<div class="flex flex-col items-center mt-3 mx-5">
            <label class="w-full mb-1 ml-2">Password Baru</label>
            <div class="relative w-full">
              <input
                type="password"
                class="input pr-12 w-full border col-span-10"
                placeholder="Password"
                id="newPassword"
                v-model="dataProfile.ubahPassword.newPassword"
              />
              <div
                class="absolute top-0 right-0 rounded-r w-14 h-full flex items-center justify-center bg-gray-100 dark:bg-dark-1 dark:border-dark-4 border text-gray-600 cursor-pointer"
                @click="$gpf.showPass('newPassword')"
              >
                show
              </div>
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-ubah-password')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-auto bg-theme-1 text-white"
            @click="requestUbahPassword()"
          >
            Ubah Password
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Ubah Pin -->
    <div id="modal-ubah-pin" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3"
        >
          <h2 class="font-medium text-base mr-auto">
            Ubah Pin
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-ubah-pin')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col items-center mt-3 mx-5">
            <label class="w-full mb-1 ml-2">Pin Sebelumnya</label>
            <div class="relative w-full">
              <input
                type="password"
                class="input pr-12 w-full border col-span-10"
                placeholder="Pin"
                id="oldPin"
                v-model="dataProfile.ubahPin.oldPin"
              />
              <div
                class="absolute top-0 right-0 rounded-r w-14 h-full flex items-center justify-center bg-gray-100 dark:bg-dark-1 dark:border-dark-4 border text-gray-600 cursor-pointer"
                @click="$gpf.showPass('oldPin')"
              >
                show
              </div>
            </div>
          </div>
					<div class="flex flex-col items-center mt-3 mx-5">
            <label class="w-full mb-1 ml-2">Pin Baru</label>
            <div class="relative w-full">
              <input
                type="password"
                class="input pr-12 w-full border col-span-10"
                placeholder="Pin"
                id="newPin"
                v-model="dataProfile.ubahPin.newPin"
              />
              <div
                class="absolute top-0 right-0 rounded-r w-14 h-full flex items-center justify-center bg-gray-100 dark:bg-dark-1 dark:border-dark-4 border text-gray-600 cursor-pointer"
                @click="$gpf.showPass('newPin')"
              >
                show
              </div>
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-ubah-pin')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-auto bg-theme-1 text-white"
            @click="requestUbahPin()"
          >
            Ubah Pin
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Update Profile -->
    <div id="modal-update-profile" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3"
        >
          <h2 class="font-medium text-base mr-auto">
            Update Profile
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-update-profile')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Nama</label>
            <input
              type="email"
              class="input w-full border flex-1"
              placeholder="Nama"
              v-model="exDataUP.name"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Phone</label>
            <input
              type="email"
              class="input w-full border flex-1"
              placeholder="Phone"
              v-model="exDataUP.phone"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Email</label>
            <input
              type="email"
              class="input w-full border flex-1"
              placeholder="Email"
              v-model="exDataUP.email"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Photo</label>
            <div class="flex flex-col w-10/12">
              <div class="flex items-center mb-2">
                <input
                  type="file"
                  class="button w-10 border flex-1 hidden"
                  placeholder="Photo"
                  id="fileBannerDM"
                  v-on:change="handleFile($event)"
                />
                <button
                  v-if="exDataUP.photo === ''"
                  @click="chooseBanner()"
                  class="button bg-theme-1 hover:bg-primary text-white w-28 whitespace-nowrap"
                >
                  Choose File
                </button>
                <button
                  v-else
                  @click="removeBanner()"
                  class="button bg-theme-1 hover:bg-primary text-white w-32 whitespace-nowrap"
                >
                  Remove Photo
                </button>
                <span class="text-sm ml-2 truncate">{{ exDataUP.nameBanner }}</span>
              </div>
              <span class="text-sm">Maks Ukuran File Photo = 1MB.</span>
              <span
                v-if="exDataUP.oversizeBanner"
                class="text-sm text-red-700"
              >Ukuran Photo melebihi 1MB, Silahkan Pilih Banner kembali.</span>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Preview Image</label>
            <div class="show-image w-80">
              <img
                id="showImage"
                :src="exDataUP.photo"
                alt
              />
            </div>
          </div>
        </div>
        <div
          class="px-5 py-3 text-center"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-update-profile')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-auto bg-theme-1 text-white"
            @click="updateProfileApi()"
          >
            Update Profile
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { api } from "@/utils/api-config";
import { gpf } from "@/utils/global-functions";
import { VueCookieNext as $ck } from "vue-cookie-next";

export default defineComponent({
  setup() {
		const ld = api.getLoginData();
		const dataProfile = reactive({
      data: {
        id: ld.id,
        username: ld.username,
        email: ld.email,
        name: ld.name,
        phone: ld.phone,
        photo: ld.photo,
        role: ld.role,
        token: ld.token,
      },
			emailLupaPassword: "",
			ubahPassword: {
				oldPassword: "",
				newPassword: "",
			},
      ubahPin: {
				oldPin: "",
				newPin: "",
			},
		});

		const getDataProfile = async () => {
			cash("#loadingDP").show();
			const hide = () => cash("#loadingDP").hide();

			const url = api.publicProfile;
			const hitApi = await api.hitApiGet(url, hide);
			const rs = hitApi.data.response;
			const ex = dataProfile.data;
			ex.username = rs.username;
			ex.email = rs.email;
			ex.name = rs.name;
			ex.phone = rs.phone;
			ex.photo = rs.photo;

      $ck.setCookie("login-data", ex);
		};
		getDataProfile();

		const modalLupaPassword = () => {
			dataProfile.emailLupaPassword = ''; 
			gpf.showModal('#modal-lupa-password-profile');
		}

		const requestLupaPassword = async () => {
			gpf.gLoading.show();
			const hide = () => gpf.gLoading.hide();

			const params = new URLSearchParams();
      params.append("email", dataProfile.emailLupaPassword);

			const url = api.publicForgotPassword;
			const hitApi = await api.hitApiPut(url, params, hide);
			gpf.hideModal('#modal-lupa-password-profile');
			gpf.handleSds(hitApi, 'toast');

		};

		const modalUbahPassword = () => {
			const ex = dataProfile.ubahPassword;
			ex.oldPassword = ''; 
			ex.newPassword = ''; 
			gpf.showModal('#modal-ubah-password');
		}

		const requestUbahPassword = async () => {
			gpf.gLoading.show();
			const hide = () => gpf.gLoading.hide();

			const ex = dataProfile.ubahPassword;
			const params = new URLSearchParams();
      params.append("oldPassword", ex.oldPassword);
      params.append("newPassword", ex.newPassword);

			const url = api.publicResetPassword;
			const hitApi = await api.hitApiPut(url, params, hide);
			gpf.hideModal('#modal-ubah-password');
			gpf.handleSds(hitApi, 'toast');

		};

    const modalUbahPin = () => {
			const ex = dataProfile.ubahPin;
			ex.oldPin = ''; 
			ex.newPin = ''; 
			gpf.showModal('#modal-ubah-pin');
		}

    const requestUbahPin = async () => {
			gpf.gLoading.show();
			const hide = () => gpf.gLoading.hide();

			const ex = dataProfile.ubahPin;
			const params = new URLSearchParams();
      params.append("oldPin", ex.oldPin);
      params.append("newPin", ex.newPin);

			const url = api.publicResetPin;
			const hitApi = await api.hitApiPut(url, params, hide);
			gpf.hideModal('#modal-ubah-pin');
			gpf.handleSds(hitApi, 'toast');

		};

    return {
			dataProfile,
			modalLupaPassword,
			requestLupaPassword,
			modalUbahPassword,
			requestUbahPassword,
      modalUbahPin,
      requestUbahPin,
      getDataProfile,
    };
  },
  data() {
    return {
      exDataUP: {
        name: "",
        phone: "",
        email: "",
        photo: "",
        nameBanner: "Choose File",
        oversizeBanner: false,
        tempBanner: "",
      }
    }
  },
  methods: {
    chooseBanner() {
      const id = "fileBannerDM";
      const btn: any = document.getElementById(id);
      btn.click();
    },
    removeBanner() {
      const ex = this.exDataUP;
      ex.nameBanner = "Choose File";
      ex.photo = "";
      cash("#showImage").attr("src", "");
    },
    handleFile(evt: any ) {
      const target = evt.target;
      const files = target.files;
      const sizeFiles = target.files[0].size;
      const ex = this.exDataUP;

      if (sizeFiles > 1000000) {
        ex.oversizeBanner = true;
        this.removeBanner();
      } else {
        const nameFile = target.files[0].name;
        ex.nameBanner = nameFile;
        ex.photo = files[0];
        ex.oversizeBanner = false;
        // const bodyFormData = new FormData();
        // bodyFormData.append("files", files[0]);

        this.renderImage(target.files[0]);
      }
      // console.log("evt ", evt);
    },
    renderImage(files: any) {
      // console.log('masuk render image ', files)
      const reader = new FileReader();
      reader.onload = function(e: any) {
        cash("#showImage").attr("src", e.target.result);
      };
      reader.readAsDataURL(files);
    },
    async emptyFieldUP() {
      const dp = this.dataProfile.data;
      const ex = this.exDataUP;
      ex.name = dp.name;
      ex.phone = dp.phone;
      ex.email = dp.email;
      ex.photo = dp.photo;
      ex.nameBanner = "Choose File";
      ex.oversizeBanner = false;
      ex.tempBanner = "";
      this.removeBanner();
    },
    async modalUpdateProfile() {
      await this.emptyFieldUP();
      await gpf.showModal("#modal-update-profile");
    },
    async updateProfileApi() {
      gpf.gLoading.show();

      const hide = () => gpf.gLoading.hide();
      const ex = this.exDataUP;
      const body = new FormData();
      body.append("name", ex.name);
      body.append("phone", ex.phone);
      body.append("email", ex.email);
      body.append("photo", ex.photo);

      const url = api.publicProfile;
      const hitApi = await api.hitApiPut(url, body, hide);
      await gpf.handleSds(hitApi, 'toast');
      await gpf.hideModal("#modal-update-profile");
      await this.emptyFieldUP();
      await this.getDataProfile();

    }
  }
});
</script>
