
import { defineComponent, reactive } from "vue";
import { useRouter } from "vue-router";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const router = useRouter();

    const dataKW = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const titleTable: Array<string> = [
      "Banner",
      "Tanggal",
      "Title",
      "Deskripsi",
      "Link",
      "Status",
    ];

    const getData = async () => {
      cash("#loadingKW").show();

      const hide = () => cash("#loadingKW").hide();
      const page = dataKW.page;
      const pageSize = dataKW.pageSize;
      const search = dataKW.search;
      const url = await api.getConfigFeatureMember('memberKelasWebinar', 'featureKelasWebinar') +
        "?page=" +
        page +
        "&pageSize=" +
        pageSize +
        "&search=" +
        search;

      const hitApi = await api.hitApiGet(url, hide);
      const ds = hitApi.data.diagnostic;
      const rs = hitApi.data.response;

      dataKW.data = rs;
      dataKW.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);

    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataKW.page = p;
      getData();
    };

    const nextPage = () => {
      dataKW.page = dataKW.page + 1;
      getData();
    };

    const beforePage = () => {
      dataKW.page = dataKW.page - 1;
      getData();
    };

    const lastPage = () => {
      dataKW.page = dataKW.paginate.totalPages;
      getData();
    };

    return {
      titleTable,
      dataKW,
      getData,
      router,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
    };
  },
  data() {
    return {};
  },
  methods: {},
});
