<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data Pemasukan</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div
            class="flex flex-col md:flex-row items-center col-span-3 md:col-span-1"
          >
            <p
              class="text-base mr-auto md:mr-2 mb-3 md:mb-0 whitespace-nowrap text-left"
            >
              Filter :
            </p>
            <div class="w-full flex flex-row">
              <Litepicker
                v-model="dataDP.dateRange"
                :options="{
                  autoApply: false,
                  singleMode: false,
                  numberOfColumns: 2,
                  numberOfMonths: 2,
                  showWeekNumbers: true,
                  dropdowns: {
                    minYear: 1990,
                    maxYear: null,
                    months: true,
                    years: true,
                  },
                }"
                class="input w-56 border block"
              />
              <button
                @click="getData()"
                class="button bg-theme-1 hover:bg-primary text-white w-24 ml-auto mt-0 md:ml-2 md:mt-auto"
              >
                View
              </button>
            </div>
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataDP.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                @click="getData()"
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0 cursor-pointer"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                  v-html="k"
                ></th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataDP.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.invoice }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.member.name }} <br />
                  {{ u.member.phone }} <br />
                  {{ u.member.email }}
                </td>
                <td
                  class="border-b dark:border-dark-5 whitespace-nowrap uppercase"
                >
                  {{ u.price }}
                </td>
                <td
                  class="border-b dark:border-dark-5 whitespace-nowrap uppercase"
                >
                  {{ u.referral }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.paket }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.paymentType }} / {{ u.paymentStatus }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.createdAt, "DD - MMM - YYYY HH:mm:ss") }} / <br> {{ $h.formatDate(u.acceptedAt, "DD - MMM - YYYY HH:mm:ss") }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  <div class="flex flex-row items-center">
                    <span>{{ u.status }}</span>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block ml-2"
                      content="Konfirmasi Pemasukan"
                      v-if="u.status === 'confirm'"
                      @click="confirmDP(u)"
                    >
                      <CheckCircleIcon
                        class="w-6 h-6 cursor-pointer text-theme-9"
                      />
                    </Tippy>
                  </div>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Detail"
                      @click="detailDPApi(u)"
                    >
                      <FileIcon
                        class="w-6 h-6 cursor-pointer text-primary"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                      @click="editDPApi(u)"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer text-theme-12"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataDP.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Data Pemasukan.</p>
          </div>
          <LoadingDiv id="loadingDPadmin" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataDP.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataDP.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataDP.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataDP.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataDP.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataDP.page !== dataDP.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataDP.page !== dataDP.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataDP.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>
    <!-- Modal Add and Edit Data Pemasukan -->
    <div id="modal-data-dp" class="modal p-0">
      <div class="modal__content modal__content--xl">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Edit Data Pemasukan</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-data-dp')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="flex flex-col md:flex-row px-3 md:px-5 py-3">
          <div class="flex flex-col w-full md:w-1/2">
            <div class="flex flex-col sm:flex-row">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5">Invoice</label>
              <p class="text-gray-500">{{ exDataDP.invoice }}</p>
            </div>
            <div class="flex flex-col sm:flex-row items-start md:items-center mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5">Price</label>
              <input
                type="text"
                class="input w-full border flex-1"
                placeholder="Price"
                v-on:keyup="ctcDP('price')"
                v-model="exDataDP.price"
              />
            </div>
            <div class="flex flex-col sm:flex-row items-start md:items-center mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5"
                >Kode Unik</label
              >
              <input
                type="text"
                class="input w-full border flex-1"
                placeholder="Kode Unik"
                v-model="exDataDP.uniqueCode"
              />
            </div>
            <div class="flex flex-col sm:flex-row mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5"
                >Payment Method</label
              >
              <p class="text-gray-500">{{ exDataDP.paymentMethod }}</p>
            </div>
            <div class="flex flex-col sm:flex-row items-start md:items-center mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5"
                >Payment Type</label
              >
              <div class="w-full sm:w-1/2">
                <TailSelect
                  v-model="exDataDP.paymentType"
                  v-on:change="changeTS($event, 'paymentType')"
                  :options="{
                    classNames: 'w-full',
                  }"
                >
                  <option
                    v-for="(ss, idx) in listPayType"
                    v-bind:key="idx"
                    :value="ss.value"
                    @click="exDataDP.paymentType = ss.value"
                  >
                    {{ ss.title }}
                  </option>
                </TailSelect>
              </div>
            </div>
            <div class="flex flex-col sm:flex-row items-start md:items-center mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5">Payment Status</label>
              <div class="w-full sm:w-1/2">
                <TailSelect
                  v-model="exDataDP.paymentStatus"
                  v-on:change="changeTS($event, 'paymentStatus')"
                  :options="{
                    classNames: 'w-full',
                  }"
                >
                  <option
                    v-for="(ss, idx) in listPayStatus"
                    v-bind:key="idx"
                    :value="ss.value"
                    @click="exDataDP.paymentStatus = ss.value"
                  >
                    {{ ss.title }}
                  </option>
                </TailSelect>
              </div>
            </div>
            <div class="flex flex-col sm:flex-row items-start md:items-center mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5">Status</label>
              <div class="w-full sm:w-1/2">
                <TailSelect
                  v-model="exDataDP.status"
                  v-on:change="changeTS($event, 'status')"
                  :options="{
                    classNames: 'w-full',
                  }"
                >
                  <option
                    v-for="(ss, idx) in listStatus"
                    v-bind:key="idx"
                    :value="ss.value"
                    @click="exDataDP.status = ss.value"
                  >
                    {{ ss.title }}
                  </option>
                </TailSelect>
              </div>
            </div>

            <div 
              v-if="exDataDP.paymentMethod !== 'Virtual Account'"
              class="flex flex-col items-start mt-3"
            >
              <label class="w-full sm:w-28 sm:mr-5 font-bold">Konfirmasi :</label>
              <div class="flex flex-col pl-3 md:pl-5">
                <p class="text-gray-500"> <span class="text-gray-700">Account Name :</span> {{ exDataDP.confirm.accountName }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Account Number :</span> {{ exDataDP.confirm.accountNumber }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Bank Name :</span> {{ exDataDP.confirm.bankName }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Nominal :</span> {{ $gpf.convertToCurrency(exDataDP.confirm.nominal, "IDR ") }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Status :</span> {{ exDataDP.confirm.status }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Confirm At :</span> 
                  {{ 
                    $h.formatDate(
                      exDataDP.confirm.confirmAt,
                      "DD-MMM-YYYY HH:mm:ss"
                    ) 
                  }}
                </p>
                <img :src="exDataDP.confirm.attachment" alt="" class="rounded mt-2">
              </div>
            </div>

          </div>
          <div class="flex flex-col w-full md:w-1/2 pl-0 md:pl-5 mt-3 md:mt-0">
            <div class="flex flex-col items-start">
              <label class="w-full sm:w-28 sm:mr-5 font-bold">Member :</label>
              <div class="flex flex-col pl-3 md:pl-5">
                <p class="text-gray-500"> <span class="text-gray-700">Nama :</span> {{ exDataDP.member.name }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Phone :</span> {{ exDataDP.member.phone }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Email :</span> {{ exDataDP.member.email }}</p>
              </div>
            </div>
            
            <div class="flex flex-col mt-3">
              <label class="w-full sm:w-28 sm:mr-5 font-semibold">Payment :</label>
              <div class="w-full">
                <TailSelect
                  v-model="exDataDP.paymentId"
                  v-on:change="changeTS($event, 'paymentId')"
                  :options="{
                    classNames: 'w-full',
                  }"
                >
                  <optgroup 
                    v-for="(pp, idx) in listPayment"
                    v-bind:key="idx"
                    :label="pp.label"
                  >
                    <option
                      v-for="(ss, idx) in pp.options"
                      v-bind:key="idx"
                      :value="ss.value"
                      @click="exDataDP.payment = ss.value"
                    >
                      {{ ss.text }}
                    </option>
                  </optgroup>
                </TailSelect>
              </div>
            </div>

            <div class="flex flex-col items-start mt-3">
              <label class="w-full sm:w-28 sm:mr-5 font-bold">Referral :</label>
              <div class="flex flex-col pl-3 md:pl-5">
                <p class="text-gray-500"> <span class="text-gray-700">Code :</span> {{ exDataDP.refferal.code }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Name :</span> {{ exDataDP.refferal.name }}</p>
              </div>
            </div>

            <div class="flex flex-col mt-3">
              <label class="w-full sm:w-28 sm:mr-5 font-semibold">Paket :</label>
              <div class="w-full">
                <TailSelect
                  v-model="exDataDP.paketId"
                  v-on:change="changeTS($event, 'paketId')"
                  :options="{
                    classNames: 'w-full',
                  }"
                >
                  <option
                    v-for="(s, idx) in listPackage"
                    v-bind:key="idx"
                    :value="s.id"
                    @click="exDataDP.paket = s.id"
                  >
                    {{ s.title }} | {{ $gpf.convertToCurrency(s.price, "IDR ") }}
                  </option>
                </TailSelect>
              </div>
            </div>

            <div class="flex flex-col mt-3">
              <label class="w-full sm:w-28 sm:mr-5 font-semibold">Promo :</label>
              <div class="w-full">
                <TailSelect
                  v-model="exDataDP.promoId"
                  v-on:change="changeTS($event, 'promoId')"
                  :options="{
                    classNames: 'w-full',
                  }"
                >
                  <option
                    v-for="(s, idx) in listPromo"
                    v-bind:key="idx"
                    :value="s.id"
                    @click="exDataDP.promo = s.id"
                  >
                    <span v-if="s.type === 'percent'">
                      {{ s.code }} | {{ s.discount }}%
                    </span>
                    <span v-if="s.type === 'price'">
                      {{ s.code }} |
                      {{ $gpf.convertToCurrency(s.discount, "IDR ") }}
                    </span>
                  </option>
                </TailSelect>
              </div>
            </div>

          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-data-dp')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="updatedDP()"
          >
            Update
          </button>
        </div>
      </div>
    </div>

    <!-- Modal Detail Data Pemasukan -->
    <div id="modal-detail-dp" class="modal p-0">
      <div class="modal__content modal__content--xl">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Detail Data Pemasukan</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-detail-dp')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="flex flex-col md:flex-row px-3 md:px-5 pt-3 pb-5">
          <div class="flex flex-col w-full md:w-1/2">
            <div class="flex flex-col sm:flex-row">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5">Invoice</label>
              <p class="text-gray-500">{{ detailDP.invoice }}</p>
            </div>
            <div class="flex flex-col sm:flex-row mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5">Price</label>
              <p class="text-gray-500">
                {{ $gpf.convertToCurrency(detailDP.price, "IDR ") }}
              </p>
            </div>
            <div class="flex flex-col sm:flex-row mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5"
                >Kode Unik</label
              >
              <p class="text-gray-500">{{ detailDP.uniqueCode }}</p>
            </div>
            <div class="flex flex-col sm:flex-row mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5"
                >Payment Method</label
              >
              <p class="text-gray-500">{{ detailDP.paymentMethod }}</p>
            </div>
            <div class="flex flex-col sm:flex-row mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5"
                >Payment Type</label
              >
              <p class="text-gray-500">{{ detailDP.paymentType }}</p>
            </div>
            <div class="flex flex-col sm:flex-row mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5"
                >Payment Status</label
              >
              <p class="text-gray-500">{{ detailDP.paymentStatus }}</p>
            </div>
            <div class="flex flex-col sm:flex-row mt-3">
              <label class="w-full sm:w-28 sm:text-right sm:mr-5">Status</label>
              <p class="text-gray-500">{{ detailDP.status }}</p>
            </div>

            <div 
              v-if="detailDP.paymentMethod !== 'Virtual Account'"
              class="flex flex-col items-start mt-3"
            >
              <label class="w-full sm:w-28 sm:mr-5 font-bold">Konfirmasi :</label>
              <div class="flex flex-col pl-3 md:pl-5">
                <p class="text-gray-500"> <span class="text-gray-700">Account Name :</span> {{ detailDP.confirm.accountName }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Account Number :</span> {{ detailDP.confirm.accountNumber }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Bank Name :</span> {{ detailDP.confirm.bankName }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Nominal :</span> {{ $gpf.convertToCurrency(detailDP.confirm.nominal, "IDR ") }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Status :</span> {{ detailDP.confirm.status }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Confirm At :</span> 
                  {{ 
                    $h.formatDate(
                      detailDP.confirm.confirmAt,
                      "DD-MMM-YYYY HH:mm:ss"
                    ) 
                  }}
                </p>
                <img :src="detailDP.confirm.attachment" alt="" class="rounded mt-2">
              </div>
            </div>

          </div>
          <div class="flex flex-col w-full md:w-1/2 pl-0 md:pl-5 mt-3 md:mt-0">
            <div class="flex flex-col items-start">
              <label class="w-full sm:w-28 sm:mr-5 font-bold">Member :</label>
              <div class="flex flex-col pl-3 md:pl-5">
                <p class="text-gray-500"> <span class="text-gray-700">Nama :</span> {{ detailDP.member.name }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Phone :</span> {{ detailDP.member.phone }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Email :</span> {{ detailDP.member.email }}</p>
              </div>
            </div>

            <div class="flex flex-col items-start mt-3">
              <label class="w-full sm:w-28 sm:mr-5 font-bold">Payment :</label>
              <div class="flex flex-col pl-3 md:pl-5">
                <p class="text-gray-500"> <span class="text-gray-700">Account Name :</span> {{ detailDP.payment.accountName }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Account Number :</span> {{ detailDP.payment.accountNumber }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Bank Name :</span> {{ detailDP.payment.bankName }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Payment Type :</span> {{ detailDP.payment.paymentType }}</p>
              </div>
            </div>

            <div class="flex flex-col items-start mt-3">
              <label class="w-full sm:w-28 sm:mr-5 font-bold">Referral :</label>
              <div class="flex flex-col pl-3 md:pl-5">
                <p class="text-gray-500"> <span class="text-gray-700">Code :</span> {{ detailDP.refferal.code }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Name :</span> {{ detailDP.refferal.name }}</p>
              </div>
            </div>

            <div class="flex flex-col items-start mt-3">
              <label class="w-full sm:w-28 sm:mr-5 font-bold">Paket :</label>
              <div class="flex flex-col pl-3 md:pl-5">
                <p class="text-gray-500"> <span class="text-gray-700">Title :</span> {{ detailDP.paket.title }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Price :</span> {{ $gpf.convertToCurrency(detailDP.paket.price, "IDR ") }}</p>
              </div>
            </div>

            <div class="flex flex-col items-start mt-3">
              <label class="w-full sm:w-28 sm:mr-5 font-bold">Promo :</label>
              <div class="flex flex-col pl-3 md:pl-5">
                <p class="text-gray-500"> <span class="text-gray-700">Kode :</span> {{ detailDP.promo.code }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Discount :</span> 
                  {{
                    detailDP.promo.type === "percent"
                      ? detailDP.promo.discount + "%"
                      : $gpf.convertToCurrency(detailDP.promo.discount, "IDR ")
                  }}</p>
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>

    <!-- Modal Accept Data Pemasukan -->
    <div id="modal-confirm-dp" class="modal">
      <div class="min-h-screen flex justify-center items-center">
        <div class="box mx-auto w-auto">
          <div class="p-5 text-center">
            <CheckCircleIcon class="w-16 h-16 text-theme-9 mx-auto mt-3" />
            <div class="text-3xl mt-5">Apakah Anda yakin?</div>
            <div class="text-gray-600 mt-2">
              Ingin mengkonfirmasi data pemasukan dengan Invoice "{{ exConfirmDP.invoice }}"?
            </div>
          </div>
          <div class="px-5 pb-8 text-center">
            <button
              type="button"
              data-dismiss="modal"
              class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
              @click="$gpf.hideModal('#modal-confirm-dp')"
            >
              Cancel
            </button>
            <button
              type="button"
              class="button w-24 bg-theme-9 text-white"
              @click="confirmDPApi()"
            >
              Yakin
            </button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { dataStatus, dataPayType, dataPayStatus } from "./data-pemasukan";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import { helper as $h } from "@/utils/helper";
import axios from "axios";

export default defineComponent({
  setup() {
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listStatus = dataStatus();
    const listPayType = dataPayType();
    const listPayStatus = dataPayStatus();

    const titleTable: Array<string> = [
      "Invoice",
      "Member",
      "Price",
      "Referal",
      "Paket",
      "Payment Type / <br> Payment Status",
      "Created At / <br> Accepted At",
      "Status",
      "#",
    ];

    const dataDP = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      dateRange: "",
    });

    const date = new Date();
    const firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
    const lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);

    dataDP.dateRange = `${$h.formatDate(firstDay.toString(), 'DD MMM, YYYY')} - ${$h.formatDate(lastDay.toString(), 'DD MMM, YYYY')}`;

    const getData = async () => {
      cash("#loadingDPadmin").show();

      // console.log("dateRange ", dataDP.dateRange);
      // console.log("firstDay lastDay ", firstDay, lastDay);

      const page = dataDP.page;
      const pageSize = dataDP.pageSize;
      const search = dataDP.search;
      const since = $h.formatDate(dataDP.dateRange.split('-')[0], 'YYYY-MM-DD');
      const until = $h.formatDate(dataDP.dateRange.split('-')[1], 'YYYY-MM-DD');
      axios
        .get(
          api.adminPemasukan +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize +
            "&since=" +
            since +
            "&until=" +
            until,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataDP.data = rs.map((e: any) => {
            e.price = gpf.convertToCurrency(e.price, "IDR ");
            return e;
          });
          dataDP.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataDP.paginate ", dataDP.paginate);
          cash("#loadingDPadmin").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingDPadmin").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataDP.page = p;
      getData();
    };

    const nextPage = () => {
      dataDP.page = dataDP.page + 1;
      getData();
    };

    const beforePage = () => {
      dataDP.page = dataDP.page - 1;
      getData();
    };

    const lastPage = () => {
      dataDP.page = dataDP.paginate.totalPages;
      getData();
    };

    return {
      titleTable,
      getData,
      dataDP,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listStatus,
      listPayType,
      listPayStatus,
    };
  },
  data() {
    return {
      // for modal data bs
      section: "add",
      msgSuccess: "",

      detailDP: {
        invoice: "",
        price: "",
        uniqueCode: 0,
        paymentMethod: "",
        paymentType: "",
        paymentStatus: "",
        status: "",
        createdAt: "",
        member: {
          name: "",
          phone: "",
          email: "",
        },
        payment: {
          id: 0,
          code: "",
          accountNumber: "",
          accountName: "",
          bankName: "",
          paymentType: "",
        },
        refferal: {
          id: 0,
          code: "",
          name: "",
        },
        paket: {
          id: 0,
          title: "",
          price: 0,
        },
        promo: {
          id: 0,
          code: "",
          discount: 0,
          type: "",
        },
        confirm: {
          nominal: 0,
          accountNumber: "",
          accountName: "",
          bankName: "",
          attachment: "",
          status: "",
          confirmAt: "",
        },
      },

      exDataDP: {
        invoice: "",
        price: "",
        uniqueCode: "",
        paymentMethod: "",
        paymentType: "",
        paymentStatus: "",
        status: "",
        createdAt: "",
        member: {
          name: "",
          phone: "",
          email: "",
        },
        paymentId: "",
        payment: {
          id: 0,
          code: "",
          accountNumber: "",
          accountName: "",
          bankName: "",
          paymentType: "",
        },
        paketId: "",
        paket: {
          id: 0,
          title: "",
          price: 0,
        },
        promoId: "",
        promo: {
          id: 0,
          code: "",
          discount: 0,
          type: "",
        },
        refferal: {
          id: 0,
          code: "",
          name: "",
        },
        confirm: {
          nominal: 0,
          accountNumber: "",
          accountName: "",
          bankName: "",
          attachment: "",
          status: "",
          confirmAt: "",
        },
      },

      listPackage: [],
      listPayment: [],
      listPromo: [],

      exConfirmDP: {
        invoice: "",
      }
    };
  },
  methods: {
    changeTS(v: any, f: any) {
      const val = v.detail.args[1].key;
      const ex: any = this.exDataDP;
      ex[f] = val;
    },
    ctcDP(f: string) {
      const ex: any = this.exDataDP;
      ex[f] = gpf.ctc(ex[f]);
    },
    async editDPApi(data: any) {
      this.section = "edit";
      gpf.gLoading.show();

      axios
        .get(api.adminPemasukan + data.invoice, this.configGet)
        .then(async (result: any) => {
          const rs = result.data.response;
          // console.log("response get dpp ", rs);

          await this.populateDataDP(rs);

          const qpPromo = '?refid=' + this.exDataDP.refferal.id;
          this.listPackage = await api.getPackage();
          this.listPayment = await api.getPayment();
          this.listPromo = await api.getPromo(qpPromo);

          await gpf.showModal("#modal-data-dp");
          gpf.gLoading.hide();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    async populateDataDP(data: any) {
      const ex = this.exDataDP;
      const rs = data;

      ex.invoice = rs.invoice;
      ex.price = gpf.ctc(rs.price);
      ex.uniqueCode = rs.uniqueCode;
      ex.paymentMethod = rs.paymentMethod;
      ex.paymentType = rs.paymentType;
      ex.paymentStatus = rs.paymentStatus;
      ex.status = rs.status;
      ex.createdAt = rs.createdAt;
      ex.member = rs.member;
      ex.payment = rs.payment;
      ex.refferal = rs.refferal;
      ex.paket = rs.paket;
      ex.promo = rs.promo;
      if ( rs.confirm !== null ) {
        ex.confirm = rs.confirm;
      }

      ex.paketId = rs.paket.id.toString();
      ex.paymentId = rs.payment.id.toString();
      ex.promoId = rs.promo.id.toString();
    },
    updatedDP(): void {
      gpf.gLoading.show();
      const ex = this.exDataDP;
      const body = new FormData();
      body.append("harga", gpf.btc(ex.price));
      body.append("kodeUnik", ex.uniqueCode);
      body.append("status", ex.status);
      body.append("paymentType", ex.paymentType);
      body.append("paymentStatus", ex.paymentStatus);
      body.append("packageId", ex.paketId.toString());
      body.append("paymentId", ex.paymentId.toString());
      body.append("promoId", ex.promoId.toString());

      axios
        .put(api.adminPemasukan + ex.invoice, body, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-dp");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    detailDPApi(data: any): void {
      gpf.gLoading.show();

      axios
        .get(api.adminPemasukan + data.invoice, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);

          this.detailDP = rs;

          await gpf.showModal("#modal-detail-dp");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleEds(error);
        });
    },
    async confirmDP(data: any) {
      const ex = this.exConfirmDP;
      ex.invoice = data.invoice;

      gpf.showModal("#modal-confirm-dp");
    },
    async confirmDPApi() {
      gpf.gLoading.show();

      const hide = () => gpf.gLoading.hide();
      const ex = this.exConfirmDP;
      const url = api.adminAcceptTransaksi + ex.invoice;
      const hitApi = await api.hitApiGet(url, hide);
      await gpf.handleSds(hitApi);
      gpf.hideModal("#modal-confirm-dp");
      await this.getData();
    },
  },
});
</script>
