        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            Lupa Password
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-lupa-password-profile')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Email</label>
            <input
              type="email"
              class="input w-full border flex-1"
              placeholder="Email"
              v-model="dataProfile.emailLupaPassword"
							v-on:keyup.enter="requestLupaPassword()"
            />
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gpf.hideModal('#modal-lupa-password-profile')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-auto bg-theme-1 text-white"
            @click="requestLupaPassword()"
          >
            Request
          </button>
        </div>
      </div>
    </div>

  </div>
  <!-- END: Top Bar -->
</template>

<script lang="ts">
import { defineComponent, ref, computed, watch, reactive } from "vue";
import { useStore } from "@/store";
import { useRouter } from "vue-router";
import { api } from "@/utils/api-config";
import { gpf } from "@/utils/global-functions";

export default defineComponent({
  setup() {
    const role = api.getRole();
    const router = useRouter();
    const searchDropdown = ref(false);
    const store = useStore();
    const activeTitle = computed(() => store.state.sideMenu.activeTitle);
    const activeUrlSubMenu = computed(
      () => store.state.sideMenu.activeUrlSubMenu
    );
    const ld = api.getLoginData();
    const photoUser = ld.photo;
    const nameUser = ld.name;
    const emailUser = ld.email;
    const dataProfile = reactive({
			emailLupaPassword: "",
		});

    const showSearchDropdown = () => {
      searchDropdown.value = true;
    };

    const hideSearchDropdown = () => {
      searchDropdown.value = false;
    };

    const gotoPageAkunBank = () => {
      router.push(`/${role}/akun-bank`);
    }

    const gotoPageProfile = () => {
      router.push(`/${role}/profile`);
    }

    // let count = 0;

    watch(
      computed(() => store.state.sideMenu.activeTitle), () => {
        // console.log('kepanggil nih, ', count++)
      }
    );

    const modalLupaPassword = () => {
			dataProfile.emailLupaPassword = ''; 
			gpf.showModal('#modal-lupa-password-profile');
		}

		const requestLupaPassword = async () => {
			gpf.gLoading.show();
			const hide = () => gpf.gLoading.hide();

			const params = new URLSearchParams();
      params.append("email", dataProfile.emailLupaPassword);

			const url = api.publicForgotPassword;
			const hitApi = await api.hitApiPut(url, params, hide);
			gpf.hideModal('#modal-lupa-password-profile');
			gpf.handleSds(hitApi);

		};

    return {
      searchDropdown,
      showSearchDropdown,
      hideSearchDropdown,
      activeTitle,
      activeUrlSubMenu,
      photoUser,
      nameUser,
      emailUser,
      role,
      gotoPageAkunBank,
      gotoPageProfile,
      dataProfile,
      modalLupaPassword,
      requestLupaPassword,
    };
  },
  methods: {
    logout() {
      api.logout();
      cash(".dropdown-box").dropdown("hide");
    }
  }
});
</script>
