<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data Hasil Tryout</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div class="flex flex-col items-center col-span-3 md:col-span-1">
            <p class="text-base mr-auto mb-1 md:mb-0">Tipe :</p>
            <div class="w-full flex flex-row">
              <TailSelect
                v-model="dataHT.type"
                :options="{
                  classNames: 'w-full',
                  search: true,
                }"
              >
                <option
                  v-for="(k, idx) in dataHT.listTypeTryout"
                  v-bind:key="idx"
                  :value="k.kode"
                >
                  {{ k.title }}
                </option>
              </TailSelect>
              <button
                @click="getData()"
                class="button bg-theme-1 hover:bg-primary text-white w-24 ml-2"
              >
                View
              </button>
            </div>
          </div>
          <div class="w-full col-span-3 md:col-span-2">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataHT.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                @click="getData()"
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0 cursor-pointer"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataHT.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.kode }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.nama }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.jenis }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.totalSkor }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.status }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ $h.formatDate(u.tanggal, "DD MMM, YYYY") }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Detail"
                      @click="detailHTApi(u)"
                    >
                      <FileIcon class="w-6 h-6 cursor-pointer text-primary" />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataHT.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Hasil Tryout.</p>
          </div>
          <LoadingDiv id="loadingHT" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataHT.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataHT.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataHT.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataHT.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataHT.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataHT.page !== dataHT.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataHT.page !== dataHT.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataHT.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

    <!-- Modal Detail Hasil Tryout -->
    <div id="modal-detail-ht" class="modal p-0">
      <div class="modal__content modal__content--xl">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">Detail Hasil Tryout</h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gpf.hideModal('#modal-detail-ht')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 pt-3 pb-5 flex flex-col md:flex-row">
          <div class="flex flex-col w-full md:w-1/2">
            <div
              v-for="(h, idx) in exDetailHT.hasil"
              :key="idx"
              class="flex flex-col sm:flex-row items-start mt-2 w-full"
            >
              <label class="w-full sm:w-44 sm:mr-5">{{ h.label }}</label>
              <p class="text-gray-500">{{ h.value }}</p>
            </div>
          </div>
          <div class="flex flex-col w-full md:w-1/2 pl-0 md:pl-5">
            <div class="flex flex-col items-start">
              <label class="w-full sm:w-28 sm:mr-5 font-bold">Summary :</label>
              <div class="flex flex-col pl-3 md:pl-5">
                <p class="text-gray-500"> <span class="text-gray-700">Maks Skor :</span> {{ exDetailHT.summary.maksSkor }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Total Skor :</span> {{ exDetailHT.summary.totalSkor }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Jumlah Benar :</span> {{ exDetailHT.summary.jmlBenar }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Jumlah Skip :</span> {{ exDetailHT.summary.jmlSkip }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Jumlah Salah :</span> {{ exDetailHT.summary.jmlSalah }}</p>
                <p class="text-gray-500"> <span class="text-gray-700">Status :</span> {{ exDetailHT.summary.status }}</p>
              </div>
            </div>
            <div class="flex flex-col sm:flex-row items-start mt-2 w-full">
              <label class="w-full sm:w-32 sm:mr-5">Tipe</label>
              <p class="text-gray-500">{{ exDetailHT.type }}</p>
            </div>
            <div class="flex flex-col sm:flex-row items-start mt-1 w-full">
              <label class="w-full sm:w-32 sm:mr-5">Bidang</label>
              <p class="text-gray-500">{{ exDetailHT.bidang }}</p>
            </div>
            <div class="flex flex-col sm:flex-row items-start mt-1 w-full">
              <label class="w-full sm:w-32 sm:mr-5">Start Date</label>
              <p class="text-gray-500">{{ $h.formatDate(exDetailHT.startDate, 'DD-MMM, YYYY HH:mm:ss') }}</p>
            </div>
            <div class="flex flex-col sm:flex-row items-start mt-1 w-full">
              <label class="w-full sm:w-32 sm:mr-5">End Date</label>
              <p class="text-gray-500">{{ $h.formatDate(exDetailHT.endDate, 'DD-MMM, YYYY HH:mm:ss') }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();

    const titleTable: Array<string> = [
      "Kode",
      "Nama",
      "Jenis",
      "Total Skor",
      "Status",
      "Tanggal",
      "#",
    ];

    const dataHT = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      type: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      listTypeTryout: [] as any,
    });

    const getData = async () => {
      cash("#loadingHT").show();

      const page = dataHT.page;
      const pageSize = dataHT.pageSize;
      const search = dataHT.search;
      const type = dataHT.type;
      const url =
        api.adminHasilTryout +
        "?search=" +
        search +
        "&page=" +
        page +
        "&pageSize=" +
        pageSize +
        "&type=" +
        type;

      const hide = () => cash("#loadingHT").hide();
      const hitApi = await api.hitApiGet(url, hide);
      const ds = hitApi.data.diagnostic;
      const rs = hitApi.data.response;

      dataHT.data = rs;
      dataHT.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
    };

    const getListTypeTryout = async () => {
      gpf.gLoading.show();

      const rs = await api.getJenisTryout();

      dataHT.type = rs[0].kode;
      dataHT.listTypeTryout = rs;

      await getData();
      gpf.gLoading.hide();
    };
    getListTypeTryout();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataHT.page = p;
      getData();
    };

    const nextPage = () => {
      dataHT.page = dataHT.page + 1;
      getData();
    };

    const beforePage = () => {
      dataHT.page = dataHT.page - 1;
      getData();
    };

    const lastPage = () => {
      dataHT.page = dataHT.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataHT,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
    };
  },
  data() {
    return {
      exDetailHT: {
        type: "",
        bidang: "",
        startDate: "",
        endDate: "",
        summary: {
          maksSkor: 0,
          totalSkor: 0,
          jmlBenar: 0,
          jmlSkip: 0,
          jmlSalah: 0,
          status: "",
        },
        hasil: [],
      },
    };
  },
  methods: {
    async detailHTApi(data: any) {
      gpf.gLoading.show();
      const hide = () => gpf.gLoading.hide();

      const url = api.adminHasilTryout + data.kode;
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;

      this.exDetailHT = rs;
      await gpf.showModal("#modal-detail-ht");
    },
  },
});
</script>
